:global(#top-level-details).has_multiStepCoverScreen > [data-template], :global(#top-level-details).has_itemBasedCoverScreen > [data-template], :global(#top-level-details).has_multiStepScoreScreen > [data-template], :global(#top-level-details).has_itemBasedScoreScreen > [data-template], :global(#top-level-details).has_timedPlaylistEndScreen > [data-template] {
  min-height: 60px;
  flex-grow: 0;
  height: 0;
}
:global(#top-level-details):not(.windowed) > * {
  width: 100%;
}
:global(#top-level-details).hostedByScorm {
  border: 1px solid #9a9a9a;
}

.windowed {
  width: 100vw;
  height: 100vh;
  background-image: var(--dragHandle_background_image);
  background-position: top left, top right, bottom right, bottom left, top center, right center, bottom center, left center;
  background-size: auto, auto, auto, auto, 8px, 8px, 8px, 8px;
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, repeat-x, repeat-y, repeat-x, repeat-y, repeat;
}
.windowed > * {
  margin: 0 8px 0 8px;
  width: calc(100% - 16px);
}
.windowed > *:first-child {
  margin: 8px 8px 0 8px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.windowed > *:last-child {
  margin: 0 8px 8px 8px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}
.windowed > *:only-child {
  margin: 8px 8px 8px 8px;
}
.windowed:global(#top-level-details) {
  padding-top: 0;
  padding-bottom: 0;
}

:global(.windowDragHandle) {
  position: absolute;
  top: 0;
  left: -8px;
  width: 64px;
  height: 64px;
}

:global(.windowDragHandle) path {
  stroke: var(--ux-color-primary);
}

:global(.windowDragHandle) polygon {
  fill: var(--ux-color-primary);
}

@media only screen and (max-width: 275px) {
  .windowed > * {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 0.2s, opacity 0.2s linear;
  }
}
@media only screen and (max-height: 250px) {
  .windowed:not([data-activity-mode=standAloneMedia_audio]) > * {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s linear 0.2s, opacity 0.2s linear;
  }
}

