  position: relative;
  background-color: var(--icon);
  color: var(--icon_content);
  fill: var(--icon_content);
}
.standardIcon:active:not(:disabled) {
  background-color: var(--icon_active);
  color: var(--icon_content_active);
  fill: var(--icon_content_active);
}
.standardIcon:hover:not(:disabled) {
  background-color: var(--icon_hover);
  color: var(--icon_content_hover);
  fill: var(--icon_content_hover);
}
.standardIcon:focus:not(:disabled) {
