.titleBar {
  background: #fff;
  height: 60px;
  text-align: center;
  flex-shrink: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 48px;
  bottom: 0;
  /* Prevent user from selecting text on the titleBar */
  -webkit-user-select: none;
}
.titleBar > * {
  display: flex;
  align-items: center;
  justify-content: center;
}
.titleBar:first-child {
  border-bottom: 1px solid #9a9a9a;
}
.titleBar:last-child {
  border-top: 1px solid #9a9a9a;
}
.titleBar .titleBar_activitySetTitle {
  position: absolute;
  left: 12px;
  max-width: calc(100% - 64px - 32px);
}
.titleBar .titleBar_activitySetTitle p {
  display: inline-block;
  font-size: 0.875rem;
  margin: 0;
