/* pagination area loader - appears until PLAYER_STATUS.rcfLoaded is set */
.paginationLoader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.paginationLoader div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #aaa;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.paginationLoader div:nth-child(1) {
  left: 8px;
  animation: paginationLoaderAnimation1 0.6s infinite;
}

.paginationLoader div:nth-child(2) {
  left: 8px;
  animation: paginationLoaderAnimation2 0.6s infinite;
}

.paginationLoader div:nth-child(3) {
  left: 32px;
  animation: paginationLoaderAnimation2 0.6s infinite;
}

.paginationLoader div:nth-child(4) {
  left: 56px;
  animation: paginationLoaderAnimation3 0.6s infinite;
}

@keyframes paginationLoaderAnimation1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes paginationLoaderAnimation2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
@keyframes paginationLoaderAnimation3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
