    margin-left: 8px;
  }
}
.scoreScreen_individualScoreDetails {
  margin-top: 38px;
  padding-top: 0;
  width: 100%;
}
.scoreScreen_individualScoreDetails ul {
  display: table;
  border-collapse: separate;
  border-spacing: 20px 0;
  text-align: center;
  margin: auto;
}
.scoreScreen_individualScoreDetails li {
  display: table-row;
}
.scoreScreen_individualScoreDetails span {
  display: table-cell;
}
.scoreScreen_individualScoreDetails .linkStep {
  color: var(--coverScoreScreen_linkStep);
  font-weight: 600;
  cursor: pointer;
}
.scoreScreen_individualScoreDetails .linkStep:hover {
  text-decoration: underline;
}
.scoreScreen_individualScoreDetails :global(.scoreCorrect),
.scoreScreen_individualScoreDetails :global(.scoreWrong),
.scoreScreen_individualScoreDetails :global(.scoreOpenGradable) {
  display: flex;
  justify-content: center;
  width: 24px;
}

.scoreScreen_correctText,
.scoreScreen_wrongText {
  vertical-align: middle;
}

.scoreScreen_correctIcon svg,
.scoreScreen_wrongIcon svg {
  vertical-align: middle;
  width: 22px;
  height: 25px;
}

.scoreScreen_reward .reward_text {
  font-size: 1rem;
  margin: 89px auto 37px;
  text-align: center;
  position: relative;
}
.scoreScreen_reward .reward_text:empty {
  display: none;
}

.scoreScreen_reward .reward_badge {
  margin: 0 auto;
  max-width: var(--coverScoreScreen_svg_desktop);
  height: var(--coverScoreScreen_svg_desktop);
  background-color: var(--secondaryOutline_active);
  border-radius: 50%;
  padding: 16px;
}
.scoreScreen_reward .reward_badge svg {
  width: 100%;
  height: 100%;
}

.scoreScreen_reward.none .reward_badge svg :global(#rewardIcon_4),
.scoreScreen_reward.good .reward_badge svg :global(#rewardIcon_3),
.scoreScreen_reward.great .reward_badge svg :global(#rewardIcon_2),
.scoreScreen_reward.excellent .reward_badge svg :global(#rewardIcon_1) {
  visibility: visible;
}

/*
.itemBasedScoreScreen_score_completed_with_text {
	margin-top: 34px;
	text-align: center;
}
 */
.scoreScreen_score_percentageContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 34px auto 10px;
  padding: 0;
  /*
  	.itemBasedScoreScreen_score_completed_with_text + & {
  		margin: 1rem auto 2rem;
  	}
  */
}
.scoreScreen_score_percentageContainer svg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.scoreScreen_score_percentageContainer svg path {
  stroke-linecap: round;
  transition: stroke 0.5s linear, stroke-dasharray 0.5s linear;
  transition-delay: 100ms;
}
.scoreScreen_score_percentageContainer:global(.p0) svg path {
  stroke: transparent;
}

.scoreScreen_score_percentageContent {
  width: 168px;
  height: 168px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  line-height: normal;
}
.scoreScreen_score_percentageContent .score_correctText {
  display: block;
  width: 100%;
  text-align: center;
}
.scoreScreen_score_percentageContent .score_percentageChar {
  font-size: 2rem;
  margin-left: 1.2px;
}
.scoreScreen_score_percentageContent .score_percentageValue {
  font-size: 3.375rem;
  line-height: 68px;
  font-weight: 700;
  margin-right: 1.2px;
}
.scoreScreen_score_percentageContent .score_correctText {
  font-weight: 700;
  width: 100%;
}

.itemBasedScoreScreen_score_accuracyText {
  margin-top: 2rem;
}

.scoreScreen_teacherMarkPendingNotice {
  margin-top: 56px;
}
.scoreScreen_teacherMarkPendingNotice .scoreStatus {
  vertical-align: text-bottom;
  padding-right: 8px;
  line-height: 1;
}

/* cover and score screen background border */
.coverScreen_container,
.scoreScreen_container,
.timedPlaylistEndScreen_container {
  background: linear-gradient(135deg, var(--coverScoreScreen_backgroundGradient_1), var(--coverScoreScreen_backgroundGradient_2));
}

.coverScreen_itemBased.itemBased_challenge,
.scoreScreen_itemBased.itemBased_challenge {
  background: linear-gradient(135deg, var(--coverScoreScreen_challenge_backgroundGradient_1), var(--coverScoreScreen_challenge_backgroundGradient_2));
}

.coverScreen,
.scoreScreen,
.timedPlaylistEndScreen {
  position: absolute;
  width: calc(100% - 32px);
  height: calc(100% - 32px);
  padding: 16px;
  overflow-y: auto;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.15);
  /* scrollbars styling */
  scrollbar-width: thin;
  scrollbar-color: #c7c7c7 #f1f1f1;
}
.coverScreen p,
.scoreScreen p,
.timedPlaylistEndScreen p {
  margin: 0;
}
@media only screen and (min-width: 650px) {
  .coverScreen,
  .scoreScreen,
  .timedPlaylistEndScreen {
    width: 618px;
  }
}
.coverScreen::-webkit-scrollbar,
.scoreScreen::-webkit-scrollbar,
.timedPlaylistEndScreen::-webkit-scrollbar {
  width: 10px;
  background-color: #f1f1f1;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.coverScreen::-webkit-scrollbar-thumb,
.scoreScreen::-webkit-scrollbar-thumb,
.timedPlaylistEndScreen::-webkit-scrollbar-thumb {
  background-color: #c7c7c7;
  border-radius: 8px;
}
@media (hover: hover) {
  .coverScreen::-webkit-scrollbar-thumb:hover,
  .scoreScreen::-webkit-scrollbar-thumb:hover,
  .timedPlaylistEndScreen::-webkit-scrollbar-thumb:hover {
    background-color: #7d7d7d;
  }
}
.coverScreen::-webkit-scrollbar-button, .coverScreen::-webkit-scrollbar-track, .coverScreen::-webkit-scrollbar-track-piece,
.scoreScreen::-webkit-scrollbar-button,
.scoreScreen::-webkit-scrollbar-track,
.scoreScreen::-webkit-scrollbar-track-piece,
.timedPlaylistEndScreen::-webkit-scrollbar-button,
.timedPlaylistEndScreen::-webkit-scrollbar-track,
.timedPlaylistEndScreen::-webkit-scrollbar-track-piece {
  display: none;
}

.timedPlaylistEndScreen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-direction: column;
}

/* ItemBased Cover styles */
.coverScreen_itemBased .coverScreen {
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.coverScreen_activitySetTitle,
.scoreScreen_activitySetTitle,
.itemBased_practice .coverScreen_activityModeLabel,
.itemBased_test .coverScreen_activityModeLabel,
.itemBased_challenge .coverScreen_activityModeLabel {
  margin: 16px auto 0;
}

.coverScreen_activitySetTitle,
.scoreScreen_activitySetTitle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.coverScreen_itemBased .coverScreen_activitySetTitle {
  padding: 0 0 8px;
}
.coverScreen_itemBased .coverScreen_activitySetTitle > span {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 68px;
}

.scoreScreen_completionText p,
.scoreScreen_activitySetTitle p {
  margin-bottom: 0;
}

.scoreScreen_reward {
  margin: 16px 0 0;
}

/* itemBased Teacher Review Mode label */
.itemBased_reviewModeLabel {
  width: 100%;
  background-color: var(--coverScoreScreen_reviewMode_background);
  color: var(--coverScoreScreen_reviewMode_text);
  font-weight: 800;
  text-align: center;
  font-size: 1.5rem;
  padding: 4px 0;
  margin-bottom: 8px;
  border-radius: 6px;
}

.loader {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  gap: 24px;
  width: auto;
  background-color: white;
  border: 2px solid #fff;
  margin: 0;
  z-index: 4;
  position: fixed;
  top: var(--rcfPlayer_fixedPositionOffset_top);
  right: var(--rcfPlayer_fixedPositionOffset_right);
  bottom: var(--rcfPlayer_fixedPositionOffset_bottom);
  left: var(--rcfPlayer_fixedPositionOffset_left);
}
.windowed .loader {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  margin: 0;
}
.loader .loader_block {
  height: 100%;
  max-height: 64px;
  aspect-ratio: 1/1;
  display: block;
  border-radius: 50%;
  background: conic-gradient(#fff 0%, var(--primary));
  -webkit-mask: radial-gradient(farthest-side, transparent calc(100% - 8px), #000 0);
  animation: s3 1s infinite linear;
}
.loader .loader_title {
  display: block;
  color: #000;
}
@keyframes s3 {
  to {
    transform: rotate(1turn);
  }
}

.fontSizeControlGroup,
.controlFontSizeMenuButton,
.changeFontSizeButton {
  position: relative;
}

.fontSizeControlGroup {
  display: var(--rcfPlayer-fontSizeControl-display, none);
  flex-direction: column;
  align-items: end;
  justify-content: center;
  height: 56px;
  position: absolute;
  right: 8px;
  z-index: 12;
}

.controlFontSizeMenuButton {
  width: 40px;
  height: 40px;
  border-color: var(--primary);
  display: flex;
  justify-content: center;
  align-items: center;
}
.controlFontSizeMenuButton svg {
  position: absolute;
  top: unset;
  right: unset;
  bottom: unset;
  left: unset;
}
.fontSizeControlMenu_active .controlFontSizeMenuButton {
  border-color: var(--ux-color-primary-pressed);
}

.fontSizeControlMenu {
  --font-size-control-menu-width: 173px;
  --font-size-control-menu-height: 49px;
  display: none;
  background: var(--ux-surface-container);
  border: 1px solid var(--ux-surface-container-border-light);
  border-radius: var(--button_border-radius);
  width: auto;
  width: var(--font-size-control-menu-width);
  height: var(--font-size-control-menu-height);
  padding: 8px;
  position: absolute;
  bottom: calc(var(--font-size-control-menu-height) * -1 + 2px);
  z-index: 2;
}
.fontSizeControlMenu_active .fontSizeControlMenu {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.changeFontSizeButton {
  width: 35px;
  height: 33px;
}
.changeFontSizeButton:focus:not(:disabled) {
  background: var(--primary_hover);
  border-color: var(--primary_border_hover);
}
.changeFontSizeButton:focus:not(:disabled) path {
  fill: var(--primary_content);
}
.changeFontSizeButton:hover:not(:disabled) {
  background: var(--primary_hover);
  border-color: var(--primary_border_hover);
}
.changeFontSizeButton:hover:not(:disabled) path {
  fill: var(--primary_content);
}

.changeFontSizeButton_active {
  background: var(--ux-item-active, var(--icon_hover));
}

