// _buttons.scss – button styles

button[class^='player__']:not(:disable) {
	cursor: pointer;
}
// Secondary / standard button styling
.standardButton {
	font-weight: 700;
	background-color: var(--secondary);
	border: var(--button_border_width) solid var(--secondary_border);
	border-radius: var(--button_border-radius);
	color: var(--secondary_content);
	fill: var(--secondary_content);
	cursor: pointer;
	max-width: 136px;
	width: 100%;
	height: 36px;
	padding: 0 15px;
	white-space: nowrap;
	position: relative;
	transition: all 0.3s ease;

	&:focus:not(:disabled),
	&:hover:not(:disabled) {
		transition: all 0.3s ease;
	}
	svg {
		position: absolute;
		right: 5px;
		top: calc((32px / 2) - (24px / 2) - var(--button_border_width) + 2px);
	}
	&:active:not(:disabled) {
		background-color: var(--secondary_active);
		border-color: var(--secondary_border_active);
		color: var(--secondary_content);
		fill: var(--secondary_content);
	}
	@media (hover: hover) {
		&:hover:not(:disabled) {
			background-color: var(--secondary_hover);
			border-color: var(--secondary_border_hover);
			color: var(--secondary_content);
			fill: var(--secondary_content);
		}
	}
	&:focus:not(:disabled) {
		outline: 2px solid var(--secondary_focused_outline);
		outline-offset: 2px;
	}
	&:disabled {
		cursor: default;
		background-color: var(--secondary_disabled);
		border-color: var(--secondary_border_disabled);
		color: var(--secondary_content_disabled);
		fill: var(--secondary_content_disabled);
	}
	+ .standardButton {
		margin-left: 4px;
		@media (min-width: 350px) {
			margin-left: 8px;
		}
		@media (min-width: 550px) {
			margin-left: 16px;
		}
	}
}

// TO DO:   Request the following buttons be given the class .primaryButton in the code
//          so we don't have to treat them as a proto-extend
// Primary button styling
.primaryButton,
.checkAnswersButton,
:not(.has_checkAnswersButton).controls_marking .storyDoneButton,
.has_disabledCheckAnswersButton.controls_marking .storyDoneButton,
.itemBasedStartButton,
.itemBasedNextButton,
.itemBasedFinishButton,
.itemBasedEndPlaylistButton,
.has_activitySetNavigation:not(.has_multiStepCoverScreen) .nextActivitySetButton,
.has_pendingMarks .nextActivitySetButton,
.has_tryAgainButton ~ .has_nextActivitySetButton .nextActivitySetButton,
.has_disabledCheckAnswersButton ~ .has_nextActivitySetButton .nextActivitySetButton,
.multiStepStartButton,
.multiStepNextActivityButton:not(:disabled),
.multiStepSubmitButton:not(:disabled),
.tryAgainButton,
.has_activitySetNavigation:not(.has_multiStepCoverScreen) .endPlaylistButton,
.timedPlaylistSubmitButton,
.timedPlaylistFinishButton,
.nextTimedPlaylistActivitySetButton,
.submitAnswersButton,
.has_activitySetNavigation:not(.has_multiStepCoverScreen) .controls:not(.has_controlsMarking) .multiStepSubmitButton,
.submitButton,
.controls:not(:has(.has_tryAgainButton ~ .has_endSingleActivityButton)) .endSingleActivityButton {
	background-color: var(--primary);
	border-color: var(--primary_border);
	color: var(--primary_content);
	fill: var(--primary_content);
	&:active:not(:disabled) {
		background-color: var(--primary_active);
		border-color: var(--primary_border_active);
		color: var(--primary_content);
		fill: var(--primary_content);
	}
	@media (hover: hover) {
		&:hover:not(:disabled) {
			background-color: var(--primary_hover);
			border-color: var(--primary_border_hover);
			color: var(--primary_content);
			fill: var(--primary_content);
		}
	}
	&:focus:not(:disabled) {
		outline: 2px solid var(--secondary_focused_outline);
		outline-offset: 2px;
	}
	&:hover:not(:disabled),
	&:focus:not(:disabled) {
		transition: all 0.3s ease;
	}
	&:disabled {
		background-color: var(--primary_disabled);
		border-color: var(--primary_border_disabled);
		color: var(--primary_content_disabled);
		fill: var(--primary_content_disabled);
	}
}

// Specific button styles
// Navigation buttons
// Icon buttons – small on mobile
.itemBasedNextButton,
.itemBasedPreviousButton,
.itemBasedFinishButton,
.nextActivitySetButton,
.multiStepNextActivityButton,
.multiStepPreviousActivityButton,
.multiStepNextActivitySetButton,
.multiStepPreviousActivitySetButton,
.multiStepSubmitButton,
.nextTimedPlaylistActivitySetButton,
.timedPlaylistFinishButton,
.previousTimedPlaylistActivitySetButton,
.lastTimedPlaylistActivitySetButton,
.timedPlaylistSubmitButton,
.endPlaylistButton {
	min-width: 38px;
	padding: 0;
	span {
		display: none;
	}
	@media (min-width: 450px) {
		padding: inherit;
		span {
			display: block;
		}
	}
	// Override for when it is the only button on screen
	*:only-child > &:only-child,
	// Override for when there are only 2 buttons on screen (in the same container)
	*:only-child > &:nth-child(2):last-child,
	*:only-child > &:nth-last-child(2):first-child,
	// Override for when there are only 2 buttons on screen (in different containers)
	*:nth-child(2):last-child > &:only-child,
	*:nth-last-child(2):first-child > &:only-child {
		padding: inherit;
		span {
			display: block;
		}
	}
}
// Left side navigation chevron
.previousTimedPlaylistActivitySetButton svg,
.lastTimedPlaylistActivitySetButton svg,
.itemBasedPreviousButton svg,
.multiStepPreviousActivityButton svg {
	left: 5px;
}
.toggleButton {
	min-width: 164px;
}
// Override for check answers button when it is the 2nd of 3 buttons
.has_checkAnswersButton:nth-child(2):nth-last-child(2) > .checkAnswersButton,
.has_checkAnswersButton:nth-child(2):nth-last-child(2) + .has_multiStepNextActivitySetButton > .multiStepNextActivityButton,
.has_checkAnswersButton:nth-child(2):nth-last-child(2) + .has_multiStepSubmitButton > .multiStepSubmitButton,
.has_submitButton:nth-child(2):nth-last-child(2) > .submitButton,
.has_submitButton:nth-child(2):nth-last-child(2) + .has_multiStepNextActivitySetButton > .multiStepNextActivityButton,
.has_submitButton:nth-child(2):nth-last-child(2) + .has_multiStepSubmitButton > .multiStepSubmitButton {
	padding: inherit;
	span {
		display: block;
	}
}
// z-index hack for show answers 1 by 1 button when focused
.showOneByOneButton:focus:not(:disabled):not(:last-of-type) {
	z-index: 1;
}
