// _theme.scss – The theme for the player when the .theme_1 class is present

.theme_4,
:global(.theme_4) {
	// fonts
	--base_fontsize: 18px;
	--base_fontfamily: 'Comic Sans MS', 'Comic Sans', 'DejaVuSans', Arial, Helvetica, sans-serif;

	// border radius
	--button_border_width: 1px;
	--button_border-radius: 30px;

	// primary colours
	--primary: #ff1a1a; // 53%
	--primary_border: #ff1a1a;
	--primary_content: #fff;

	--primary_active: #e60000; // 45%
	--primary_border_active: #cc0000; // 40%
	--primary_content_active: #fff;

	--primary_hover: #ff8080; // 76%
	--primary_border_hover: #ff8080;

	--primary_disabled: #8daff9;
	--primary_border_disabled: #8daff9;
	--primary_content_disabled: #fff;

	--primary_focused: #0b4ddc; // focused colour
	--primary_border_focused: #0062cc;
	--primary_focused_outline: #c6d7fc;

	// primary outline colours
	--primaryOutline: #fff;
	--primaryOutline_border: #ff1a1a;
	--primaryOutline_content: #ff1a1a;

	--primaryOutline_active: #ff8080;
	--primaryOutline_border_active: #e60000;
	--primaryOutline_content_active: #e60000;

	--primaryOutline_disabled: #fff;
	--primaryOutline_border_disabled: #ff8080;
	--primaryOutline_content_disabled: #ff8080;

	// secondary colours
	--secondary: #8b8b8b;
	--secondary_border: #c7c7c7;
	--secondary_content: #fff;

	--secondary_active: #4a4a4a;
	--secondary_border_active: #c7c7c7;
	--secondary_content_active: #fff;

	--secondary_hover: #ff8080; // 76%
	--secondary_border_hover: #ff8080;

	--secondary_disabled: #c7c7c7;
	--secondary_border_disabled: #c7c7c7;
	--secondary_content_disabled: #e2e2e2;

	--secondary_focused: #fff;
	--secondary_border_disabled: #c7c7c7;
	--secondary_focused_outline: #8daff9;

	// secondary outline colours
	--secondaryOutline: #fff;
	--secondaryOutline_border: #8b8b8b;
	--secondaryOutline_content: #000;

	--secondaryOutline_active: #f6f6f6;
	--secondaryOutline_border_active: #8b8b8b;
	--secondaryOutline_content_active: #212529;

	--secondaryOutline_disabled: #fff;
	--secondaryOutline_border_disabled: #c7c7c7;
	--secondaryOutline_content_disabled: #8b8b8b;

	// icon colours
	// standard icon colours
	--icon: #fff;
	--icon_active: #e2e2e2;
	--icon_hover: #e2e2e2;
	--icon_disabled: #fff;
	--icon_focused: #e2e2e2;
	--icon_content: #000;
	--icon_content_active: #000;
	--icon_content_hover: #000;
	--icon_content_disabled: #e2e2e2;
	--icon_content_focused: #000;
	--icon_focused_outline: #c6d7fc;

	// non-standard button colours
	--buttonFocus: #c6d7fc;

	// non-standard colors
	--noAttemptAvailable: #1a051d;

	// progress bar colours
	--progressBarActive: #fcdb4a;
	--itemBasedProgressBarProgress: #d1d1d1;
	--progressBar_correct: #00a417;
	--progressBar_wrong: #eb0104;
	--progressBar_pendingMarks: #fed119;
	--progressBar_partiallyCorrect: #eb0104;
	--itemBasedProgressBar_timerContainer_green: #00a417;
	--itemBasedProgressBar_timerContainer_orange: #f0ab35;
	--itemBasedProgressBar_timerContainer_red: #eb0104;
	--itemBasedProgressBar_lives: #1b60f4;
	--itemBasedProgressBar_correctCounter: #1b60f4;
	--timedPlaylistTimer_red: #eb0104;

	// progress pagination bar colours
	--paginationBar: #f1f1f1;
	--paginationBar_attempted: #b1c5ed;
	--paginationBar_active: #3c6ed2;
	--paginationBar_hover: #000;

	// cover/score screen colours
	--coverScoreScreen_rules_text: #1b60f4;
	--coverScoreScreen_rules_border: #8b8b8b;
	--coverScoreScreen_rules_background: #f6f6f6;
	--coverScoreScreen_reviewMode_text: #fff;
	--coverScoreScreen_reviewMode_background: #1b60f4;
	--coverScoreScreen_icons: #1b60f4;
	--coverScoreScreen_linkStep: #1b60f4;
	--coverScoreScreen_backgroundGradient_1: #0f597c;
	--coverScoreScreen_backgroundGradient_2: #da1b2c;
	--coverScoreScreen_challenge_backgroundGradient_1: #da1b2c;
	--coverScoreScreen_challenge_backgroundGradient_2: #fed119;

	// cover/score screen svg sizes
	--coverScoreScreen_svg_mobile: 118px;
	--coverScoreScreen_svg_desktop: 168px;

	// scoreCard
	--scoreCard_text: #fff;
	--scoreCard_background: #4a4a4a;
	--scoreCard_hover_text: #212529;
	--scoreCard_hover_background: #e2e2e2;

	// activitySet
	--navigationArrowsContainer_separator: #c7c7c7;
}
