// _fontSizeControl.scss – Styles for the font size controls

.fontSizeControlGroup,
.controlFontSizeMenuButton,
.changeFontSizeButton {
	position: relative;
}

.fontSizeControlGroup {
	// RCF-11987: fontSizeControlGroup will be hidden by default & the --rcfPlayer-fontSizeControl-display custom property
	// will be set to 'flex' by the WPs in style sets that support the feature
	display: var(--rcfPlayer-fontSizeControl-display, none);
	flex-direction: column;
	align-items: end;
	justify-content: center;
	height: 56px;
	position: absolute;
	right: 8px;
	z-index: 12;
}

.controlFontSizeMenuButton {
	width: 40px;
	height: 40px;
	border-color: var(--primary);
	display: flex;
	justify-content: center;
	align-items: center;
	svg {
		position: absolute;
		top: unset;
		right: unset;
		bottom: unset;
		left: unset;
	}
	.fontSizeControlMenu_active & {
		border-color: var(--ux-color-primary-pressed);
	}
}

.fontSizeControlMenu {
	--font-size-control-menu-width: 173px;
	--font-size-control-menu-height: 49px;

	display: none;

	background: var(--ux-surface-container);

	border: 1px solid var(--ux-surface-container-border-light);
	border-radius: var(--button_border-radius);

	width: auto;
	width: var(--font-size-control-menu-width);
	height: var(--font-size-control-menu-height);

	padding: 8px;

	position: absolute;
	bottom: calc((var(--font-size-control-menu-height) * -1) + 2px);
	z-index: 2;

	.fontSizeControlMenu_active & {
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: space-between;
	}
}
.changeFontSizeButton {
	width: 35px;
	height: 33px;
	&:focus:not(:disabled) {
		background: var(--primary_hover);
		border-color: var(--primary_border_hover);
		path {
			fill: var(--primary_content);
		}
	}
	&:hover:not(:disabled) {
		background: var(--primary_hover);
		border-color: var(--primary_border_hover);
		path {
			fill: var(--primary_content);
		}
	}
}
.changeFontSizeButton_active {
	background: var(--ux-item-active, var(--icon_hover));
}
