// _titleBar.scss – All atyles for title bar

.titleBar {
	@include container-bar;
	position: relative;
	height: 48px;
	bottom: 0;
	/* Prevent user from selecting text on the titleBar */
	-webkit-user-select: none;
	&:first-child {
		border-bottom: $grey-border;
	}
	&:last-child {
		border-top: $grey-border;
	}
	.titleBar_activitySetTitle {
		position: absolute;
		left: calc((48px / 2) - (24px / 2));
		max-width: calc(100% - 64px - 16px * 2);
	}
	.titleBar_activitySetTitle p {
		display: inline-block;
		font-size: 0.875rem;
		margin: 0;
		margin-left: 16px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		direction: rtl;
		span {
			direction: ltr;
			unicode-bidi: bidi-override;
		}
	}
	.titleBar_buttons {
		position: absolute;
		right: $button-offset;
	}
	.titleBar_fullscreenToggleButton span:nth-child(2) {
		display: none;
		/* We will need a class to toggle switching the icon and classes on the spans */
	}
	.titleBar_closeButton {
		margin-left: 10px;
	}
	.titleBar_dragIcon {
		position: relative;
		svg {
			position: relative;
			top: 1px;
		}
	}
}

.windowed .titleBar_fullscreenToggleButton :global(#maximize) {
	visibility: visible;
}
.maximized .titleBar_fullscreenToggleButton :global(#minimize) {
	visibility: visible;
}
.maximized .titleBar_fullscreenToggleButton:hover svg,
.windowed .titleBar_fullscreenToggleButton:hover svg {
	padding: 1px;
}
.titleBar_activitySetTitle p:focus:before,
.rcfActivity_activitySetTitle:focus:before {
	content: '';
	position: absolute;
	border: 2px solid var(--primary_focused_outline);
	left: -7px;
	top: -6px;
	right: -7px;
	bottom: -6px;
	border-radius: 5px;
}

/* Title Bar Electron dragging Styles */
/*Allow dragging from titleBar when windowed*/
.windowed {
	.titleBar {
		-webkit-app-region: drag;
		-webkit-user-select: none;
		.titleBar_dragIcon,
		.titleBar_activitySetTitle {
			-webkit-app-region: drag;
			-webkit-user-select: none;
			cursor: grab;
			line-height: 1rem;
			&:active {
				cursor: grabbing;
			}
		}
		.titleBar_buttons,
		button {
			-webkit-app-region: no-drag;
		}
	}
}

/* prevent dragging when maximized and add disabled styles to dragging icon */
.maximized {
	.titleBar {
		-webkit-app-region: no-drag;
		-webkit-user-select: none;
		.titleBar_dragIcon,
		.titleBar_activitySetTitle {
			-webkit-app-region: no-drag;
			-webkit-user-select: none;
		}
		.titleBar_activitySetTitle svg path {
			stroke: var(--icon_content_disabled);
		}
	}
}
