// topLevelDetails.scss – everything related to the #topLevelDetails div

:global(#top-level-details) {
	width: 100vw;
	height: 100vh;
	height: -moz-available;
	height: -webkit-fill-available;
	height: fill-available;
	// Height fix for iOS URL bar
	@supports (-webkit-touch-callout: none) {
		height: auto;
		background-color: $white;
	}
	display: flex;
	flex-direction: column;
	padding-top: env(safe-area-inset-top);
	padding-bottom: env(safe-area-inset-bottom);
	position: fixed;
	@include position-coordinates(0, 0, 0, 0);
	> [data-template],
	.noAttemptAvailable {
		flex-grow: 1;
		min-height: 0;
		display: flex;
		flex-direction: column;
		height: 100%;
		// Fix for locating pop-up displaying over player elements
		z-index: 0;
	}
	&.has_multiStepCoverScreen > [data-template],
	&.has_itemBasedCoverScreen > [data-template],
	&.has_multiStepScoreScreen > [data-template],
	&.has_itemBasedScoreScreen > [data-template],
	&.has_timedPlaylistEndScreen > [data-template] {
		min-height: 60px;
		flex-grow: 0;
		height: 0;
	}
	&:not(.windowed) > * {
		width: 100%;
	}
	&.hostedByScorm {
		border: $grey-border;
	}
}
.windowed {
	width: 100vw;
	height: 100vh;
	background-image: var(--dragHandle_background_image);
	background-position: top left, top right, bottom right, bottom left, top center, right center, bottom center, left center;
	background-size: auto, auto, auto, auto, 8px, 8px, 8px, 8px;
	background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, repeat-x, repeat-y, repeat-x, repeat-y, repeat;
	> * {
		margin: 0 $windowed-position-gap 0 $windowed-position-gap;
		width: calc(100% - #{$windowed-position-gap * 2});
	}
	> *:first-child {
		margin: $windowed-position-gap $windowed-position-gap 0 $windowed-position-gap;
		@include border-radius(4px, 4px, 0, 0);
	}
	> *:last-child {
		margin: 0 $windowed-position-gap $windowed-position-gap $windowed-position-gap;
		@include border-radius(0, 0, 4px, 4px);
	}
	> *:only-child {
		margin: $windowed-position-gap $windowed-position-gap $windowed-position-gap $windowed-position-gap;
	}
	&:global(#top-level-details) {
		padding-top: 0;
		padding-bottom: 0;
	}
}
:global(.windowDragHandle) {
	position: absolute;
	top: 0;
	left: -8px;
	width: 64px;
	height: 64px;
}
:global(.windowDragHandle) path {
	stroke: var(--ux-color-primary);
}
:global(.windowDragHandle) polygon {
	fill: var(--ux-color-primary);
}

// Hide the screen for small windows (windowed mode only)
.windowed {
	@media only screen and (max-width: 275px) {
		> * {
			@include hidden-content;
		}
	}
	@media only screen and (max-height: 250px) {
		&:not([data-activity-mode='standAloneMedia_audio']) > * {
			@include hidden-content;
		}
	}
}

// Native CSS variables for fixed position elements
:root {
	--rcfPlayer_wordBox_opacity: 0;
	--rcfPlayer_offset: 0px;
	--rcfPlayer_inset-top: env(safe-area-inset-top);
	--rcfPlayer_inset-bottom: env(safe-area-inset-bottom);
	// Fix for Altura app on chrome 66
	@supports not (top: env(safe-area-inset-top)) {
		--rcfPlayer_inset-top: 0px;
	}
	@supports not (top: env(safe-area-inset-bottom)) {
		--rcfPlayer_inset-bottom: 0px;
	}
}
.windowed {
	--rcfPlayer_offset: #{$windowed-position-gap};
	--rcfPlayer_inset-top: 0px;
	--rcfPlayer_inset-bottom: 0px;
}
.fullScreen,
.maximized {
	--rcfPlayer_offset: 0px;
}
.hostedByScorm {
	--rcfPlayer_offset: #{$hosted-by-scorm-gap};
}
.hostedByScorm.windowed {
	--rcfPlayer_offset: calc(#{$windowed-position-gap} + #{$hosted-by-scorm-gap});
}
:global(.topLevelDetails) {
	--rcfPlayer_fixedPositionOffset_top: calc(var(--rcfPlayer_offset) + var(--rcfPlayer_inset-top));
	--rcfPlayer_fixedPositionOffset_right: var(--rcfPlayer_offset);
	--rcfPlayer_fixedPositionOffset_bottom: calc(var(--rcfPlayer_offset) + var(--rcfPlayer_inset-bottom));
	--rcfPlayer_fixedPositionOffset_left: var(--rcfPlayer_offset);
}
.has_activitySetNavigation {
	--rcfPlayer_fixedPositionOffset_top: calc(var(--rcfPlayer_offset) + 48px + var(--rcfPlayer_inset-top));
}
.has_titleBar {
	--rcfPlayer_fixedPositionOffset_bottom: calc(var(--rcfPlayer_offset) + 48px + var(--rcfPlayer_inset-bottom));
}
.has_controls {
	--rcfPlayer_fixedPositionOffset_bottom: calc(var(--rcfPlayer_offset) + 60px + var(--rcfPlayer_inset-bottom));
	--rcfPlayer_wordBox_opacity: 1;
}
.has_controls.has_titleBar {
	--rcfPlayer_fixedPositionOffset_bottom: calc(var(--rcfPlayer_offset) + 108px + var(--rcfPlayer_inset-bottom));
}
// Fix for activitySetNavigation "popping in" during loading animation
.fullScreen .loader {
	--rcfPlayer_fixedPositionOffset_top: calc(var(--rcfPlayer_offset) + 48px + var(--rcfPlayer_inset-top));
}
// When title bar is on top
.titleBar:first-child ~ * {
	.has_titleBar & {
		--rcfPlayer_fixedPositionOffset_top: calc(var(--rcfPlayer_offset) + 48px + var(--rcfPlayer_inset-top));
		--rcfPlayer_fixedPositionOffset_bottom: calc(var(--rcfPlayer_offset) + var(--rcfPlayer_inset-bottom));
	}
	.has_titleBar.has_controls & {
		--rcfPlayer_fixedPositionOffset_bottom: calc(var(--rcfPlayer_offset) + 60px + var(--rcfPlayer_inset-bottom));
	}
	.has_titleBar.has_activitySetNavigation & {
		--rcfPlayer_fixedPositionOffset_top: calc(var(--rcfPlayer_offset) + 96px + var(--rcfPlayer_inset-top));
	}
}
