// _coverScoreEndScreen.scss – cover, score & screen styles
// These styles apply to the itemBased & multiStep start & score screens as well as the assessment end screens

// TO DO: these styles seriously need reorganising we have related styles jumbled about all over the place
// i.e. .coverScreenContainer styles on line ~21 & line ~305 – these two sets of styles should be next to each other

.coverScreen_multiStep,
.coverScreen_itemBased,
.coverScreen_container,
.timedPlaylistEndScreen_container {
	outline: 1px solid transparent;
}

.has_multiStepCoverScreen,
.has_multiStepScoreScreen,
.has_itemBasedCoverScreen,
.has_itemBasedScoreScreen {
	.content {
		height: 0;
	}
}

.coverScreen_container,
.scoreScreen_container,
.timedPlaylistEndScreen_container {
	@include center-flex;
	position: relative;
	flex-grow: 1;
	background: $white;
	margin: 0 auto;
	overflow-y: auto;
}
.scoreScreen {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.coverScreen_activitySetTitle,
.coverScreen_multiStepsLabel,
.coverScreen_activitySetDetails,
.coverScreen_conditionsContainer,
.coverScreen_promptContainer,
.scoreScreen_activitySetTitle,
.timedPlaylistEndScreen_playlistSubmittedSVG,
.timedPlaylistEndScreen > * {
	text-align: center;
}
.timedPlaylistEndScreen .timedPlaylistEndScreen_header,
.timedPlaylistEndScreen .timedPlaylistEndScreen_paragraph {
	max-width: 440px;
	margin: 0 auto;
}
.timedPlaylistEndScreen .timedPlaylistEndScreen_header {
	margin-top: 2rem;
	h2 {
		line-height: 2rem;
	}
}
.timedPlaylistEndScreen .timedPlaylistEndScreen_paragraph {
	margin-top: 1rem;
	line-height: 1.5rem;
}
.timedPlaylistEndScreen .timedPlaylistEndScreen_playlistSubmittedSVG,
.timedPlaylistEndScreen .timedPlaylistEndScreen_playlistTimedOutSVG,
.timedPlaylistEndScreen .timedPlaylistEndScreen_playlistFinishSVG {
	line-height: 0;
}
.scoreScreen_score > span:first-of-type {
	margin-right: 20px;
}
.coverScreen_multiStepsLabel {
	margin-top: 1rem;
}
.coverScreen_activitySetDetails ul {
	text-align: left;
	display: inline-block;
	svg {
		vertical-align: text-bottom;
		margin-right: 8px;
	}
}
.coverScreen_itemBased ~ div .itemBasedHeader {
	display: none;
}

// Item based & multi step cover screen svg
.coverScreen_activityModeLabel svg,
.coverScreen_multiStepsLabel svg,
.timedPlaylistEndScreen_playlistSubmittedSVG svg,
.timedPlaylistEndScreen_playlistTimedOutSVG svg,
.timedPlaylistEndScreen_playlistFinishSVG svg {
	margin: 0 auto;
	width: 100%;
	max-width: var(--coverScoreScreen_svg_mobile);
	max-height: var(--coverScoreScreen_svg_mobile);
}
@media only screen and (min-width: 575px) {
	.coverScreen_activityModeLabel svg {
		max-width: var(--coverScoreScreen_svg_desktop);
		max-height: var(--coverScoreScreen_svg_desktop);
	}
	.scoreScreen_reward .reward_badge {
		max-width: 168px;
		height: 168px;
	}
	.scoreScreen_reward .reward_text {
		margin: 16px auto 0;
	}
}
.coverScreen_activityModeLabel svg > g,
.coverScreen_multiStepsLabel svg > g {
	visibility: hidden;
}
// note: theme_3 is the defacto default when no theme parameter is provided to the player & should use the old style icons
:global(.theme_3) .coverScreen_activityModeLabel svg :global(.background),
:global(.theme_3) .itemBased_practice .coverScreen_activityModeLabel svg :global(.practice),
:global(.theme_3) .itemBased_test .coverScreen_activityModeLabel svg :global(.test),
:global(.theme_3) .itemBased_challenge .coverScreen_activityModeLabel svg :global(.challenge),
:global(.theme_3) .coverScreen_multiStepsLabel svg :global(.multiStep_theme_default) {
	visibility: visible;
}
// All other themes should use the new "theme_1" style icons
:global(.topLevelDetails:not(.theme_3)) .coverScreen_activityModeLabel svg :global(.container),
:global(.topLevelDetails:not(.theme_3)) .itemBased_practice .coverScreen_activityModeLabel svg :global(.practice_theme_1),
:global(.topLevelDetails:not(.theme_3)) .itemBased_test .coverScreen_activityModeLabel svg :global(.test_theme_1),
:global(.topLevelDetails:not(.theme_3)) .itemBased_challenge .coverScreen_activityModeLabel svg :global(.challenge_theme_1),
:global(.topLevelDetails:not(.theme_3)) .coverScreen_multiStepsLabel svg :global(.multiStep_theme_1) {
	visibility: visible;
}
// svg colour overrides for the new "theme_1" style icons – I added classes to the svgs to make targeting the correct paths easier
:global(.topLevelDetails:not(.theme_3)) svg {
	:global(.svgUpdateFill) {
		fill: var(--ux-icon-colour-dark, #2e61d0);
	}
	:global(.svgUpdateFill_light) {
		fill: var(--ux-icon-colour-light, #b1c5ed);
	}
}

.coverScreen .coverScreen_conditionsContainer {
	font-size: 1rem;
}
.coverScreen_promptContainer {
	@media screen and (max-width: 575px) {
		flex-grow: 1;
	}
}
.coverScreen_conditionsLabels {
	display: none;
	vertical-align: middle;
}
.coverScreen_conditionsLabels svg {
	fill: var(--coverScoreScreen_icons);
	width: 16px;
	height: 16px;
	margin-left: 6px;
}
.coverScreen_conditionsLabels svg:last-of-type {
	height: 20px;
}
.itemBased_challenge .coverScreen_conditionsLabels {
	display: inline-block;
}

.coverScreen_activitySetDescriptionContainer,
.coverScreen_rulesContainer {
	margin-top: 1rem;
	h3 {
		color: var(--coverScoreScreen_rules_text);
		margin-bottom: 0;
		padding-left: 5px;
		font-weight: 400;
	}
	h3 + .coverScreen_rules {
		margin-top: 6px;
	}
}
.coverScreen_activitySetDescription,
.coverScreen_rules {
	padding: 1rem;
	margin: 0 auto;
	border-radius: $border-radius;
	border: solid 1px var(--coverScoreScreen_rules_border);
	background-color: var(--coverScoreScreen_rules_background);
	line-height: 20px;
}

.scoreScreen_livesContainer,
.scoreScreen_individualScoreDetails,
.scoreScreen_score {
	padding: 14px 0 0;
}
.scoreScreen_lives {
	margin: 0;
	li {
		height: 48px;
		max-width: 48px;
	}
	li.life_active {
		:global(polygon.life-active) {
			fill: var(--coverScoreScreen_icons);
		}
		:global(path.life-container) {
			fill: transparent;
		}
	}
}
@media only screen and (max-width: 250px) {
	.scoreScreen_lives li + li {
		margin-left: 8px;
	}
}
.scoreScreen_individualScoreDetails {
	margin-top: 38px;
	padding-top: 0;
	width: 100%;
	ul {
		display: table;
		border-collapse: separate;
		border-spacing: 20px 0;
		text-align: center;
		margin: auto;
	}
	li {
		display: table-row;
	}
	span {
		display: table-cell;
	}
	.linkStep {
		color: var(--coverScoreScreen_linkStep);
		font-weight: 600;
		cursor: pointer;
		&:hover {
			text-decoration: underline;
		}
	}
	:global(.scoreCorrect),
	:global(.scoreWrong),
	:global(.scoreOpenGradable) {
		display: flex;
		justify-content: center;
		width: 24px;
	}
}

.scoreScreen_correctText,
.scoreScreen_wrongText {
	vertical-align: middle;
}
.scoreScreen_correctIcon svg,
.scoreScreen_wrongIcon svg {
	vertical-align: middle;
	width: 22px;
	height: 25px;
}
.scoreScreen_reward .reward_text {
	font-size: 1rem;
	margin: 89px auto 37px;
	text-align: center;
	position: relative;
	&:empty {
		display: none;
	}
}
.scoreScreen_reward .reward_badge {
	margin: 0 auto;
	max-width: var(--coverScoreScreen_svg_desktop);
	height: var(--coverScoreScreen_svg_desktop);
	background-color: $secondaryOutline_active;
	border-radius: 50%;
	padding: 16px;
	svg {
		width: 100%;
		height: 100%;
	}
}
.scoreScreen_reward.none .reward_badge svg :global(#rewardIcon_4),
.scoreScreen_reward.good .reward_badge svg :global(#rewardIcon_3),
.scoreScreen_reward.great .reward_badge svg :global(#rewardIcon_2),
.scoreScreen_reward.excellent .reward_badge svg :global(#rewardIcon_1) {
	visibility: visible;
}

/*
.itemBasedScoreScreen_score_completed_with_text {
	margin-top: 34px;
	text-align: center;
}
 */
.scoreScreen_score_percentageContainer {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 34px auto 10px;
	padding: 0;
	svg {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		path {
			stroke-linecap: round;
			transition: stroke 0.5s linear, stroke-dasharray 0.5s linear;
			transition-delay: 100ms;
		}
	}
	&:global(.p0) svg path {
		stroke: transparent;
	}
	/*
	.itemBasedScoreScreen_score_completed_with_text + & {
		margin: 1rem auto 2rem;
	}
*/
}
.scoreScreen_score_percentageContent {
	width: 168px;
	height: 168px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-content: center;
	align-items: center;
	line-height: normal;

	.score_correctText {
		display: block;
		width: 100%;
		text-align: center;
	}
	.score_percentageChar {
		font-size: 2rem;
		margin-left: 1.2px;
	}
	.score_percentageValue {
		font-size: 3.375rem;
		line-height: 68px;
		font-weight: 700;
		margin-right: 1.2px;
	}
	.score_correctText {
		font-weight: 700;
		width: 100%;
	}
}
.itemBasedScoreScreen_score_accuracyText {
	margin-top: 2rem;
}
.scoreScreen_teacherMarkPendingNotice {
	margin-top: 56px;
	.scoreStatus {
		vertical-align: text-bottom;
		padding-right: 8px;
		line-height: 1;
	}
}

/* cover and score screen background border */
.coverScreen_container,
.scoreScreen_container,
.timedPlaylistEndScreen_container {
	background: linear-gradient(135deg, $coverScoreScreen_backgroundGradient_1, $coverScoreScreen_backgroundGradient_2);
}
.coverScreen_itemBased.itemBased_challenge,
.scoreScreen_itemBased.itemBased_challenge {
	background: linear-gradient(135deg, $coverScoreScreen_challenge_backgroundGradient_1, $coverScoreScreen_challenge_backgroundGradient_2);
}
.coverScreen,
.scoreScreen,
.timedPlaylistEndScreen {
	position: absolute;
	width: calc(100% - (16px * 2));
	height: calc(100% - (16px * 2));
	padding: 16px;
	overflow-y: auto;
	background: $white;
	border-radius: 8px;
	box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.15);
	p {
		margin: 0;
	}
	@media only screen and (min-width: 650px) {
		width: calc(650px - 32px);
	}

	/* scrollbars styling */
	scrollbar-width: thin;
	scrollbar-color: #c7c7c7 #f1f1f1;
	&::-webkit-scrollbar {
		width: 10px;
		background-color: #f1f1f1;
		border-top-right-radius: 8px;
		border-bottom-right-radius: 8px;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #c7c7c7;
		border-radius: 8px;
		@media (hover: hover) {
			&:hover {
				background-color: #7d7d7d;
			}
		}
	}
	&::-webkit-scrollbar-button,
	&::-webkit-scrollbar-track,
	&::-webkit-scrollbar-track-piece {
		display: none;
	}
}
.timedPlaylistEndScreen {
	display: flex;
	flex-direction: column;
	justify-content: center;
	flex-direction: column;
}

// TO DO: Cover & score screen need to be refactored so they use the same styles
/* ItemBased Cover styles */
.coverScreen_itemBased .coverScreen {
	padding: 16px;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}

.coverScreen_activitySetTitle,
.scoreScreen_activitySetTitle,
.itemBased_practice .coverScreen_activityModeLabel,
.itemBased_test .coverScreen_activityModeLabel,
.itemBased_challenge .coverScreen_activityModeLabel {
	margin: 16px auto 0;
}

.coverScreen_activitySetTitle,
.scoreScreen_activitySetTitle {
	@include center-flex;
	width: 100%;
}
.coverScreen_itemBased .coverScreen_activitySetTitle {
	padding: 0 0 8px;
	> span {
		@include center-flex;
		min-height: 68px;
	}
}
.scoreScreen_completionText p,
.scoreScreen_activitySetTitle p {
	margin-bottom: 0;
}

.scoreScreen_reward {
	margin: 16px 0 0;
}

/* itemBased Teacher Review Mode label */
.itemBased_reviewModeLabel {
	width: 100%;
	background-color: var(--coverScoreScreen_reviewMode_background);
	color: var(--coverScoreScreen_reviewMode_text);
	font-weight: 800;
	text-align: center;
	font-size: 1.5rem;
	padding: 4px 0;
	margin-bottom: 8px;
	border-radius: 6px;
}
