// _reset.scss – resets & overides

body {
	height: 100vh;
	// Height fix for iOS URL bar
	@supports (-webkit-touch-callout: none) {
		height: -webkit-fill-available;
	}
	margin: 0;
	position: relative;
	user-select: none;
	width: 100%;
}
* {
	box-sizing: border-box;
}

// Player hotfixes
/* Disable Rubber-Band / Elastic Scroll in macOS */
html {
	overflow: hidden;
}
/* Disable android's chrome pull-down-to-refresh feature */
body {
	overscroll-behavior: contain;
}
/* Override of content's sliding Container size to fit in the RCF player */
:global(html.rcf_slidingPanelOpenRight) .content::-webkit-scrollbar,
:global(html.rcf_slidingPanelOpenLeft) .content::-webkit-scrollbar {
	z-index: -10;
}
.content :global(.slidingBlock) {
	height: auto;
}
.content :global(.slidingContainer .slidingPanel .slidingContent) {
	overflow: auto;
	padding-top: 9px;
	margin-bottom: 0;
	transform: translateZ(0);
}
/*hide box until redesing*/
.itemBasedCoverScreen_challengeReward {
	display: none;
}
/*polyfill electron's chromium missing full-screen min-width styles*/
:global(:-webkit-full-screen) {
	object-fit: contain;
	position: fixed !important;
	top: 0px !important;
	right: 0px !important;
	bottom: 0px !important;
	left: 0px !important;
	box-sizing: border-box !important;
	min-width: 0px !important;
	max-width: none !important;
	min-height: 0px !important;
	max-height: none !important;
	width: 100% !important;
	height: 100% !important;
	transform: none !important;
	margin: 0px !important;
}

// Development styles
/* Development template only */
.componentTable {
	font-size: xx-small;
	overflow-y: scroll;
	line-height: 1rem;
	button,
	div {
		position: relative;
		left: 0;
		top: 0;
	}
}
/* Development-dialog template only */
.developmentDialog {
	background: white;
}

// RCF content hotfixes

/* open-gradable player's styles */
:global(.activity .overallScore) {
	display: none;
}
:global(.activity.reviewTeacherFeedback .overallScore) {
	display: block;
}
/* fixedWordBox styles */
/* Mac X wordBox styles*/
.content :global [data-fixedwordpools='y'] .categorise .wordBox,
.content :global [data-fixedwordpools='y'] .sentenceBuilderContainer .sentenceBuilderWordBox,
.content :global [data-fixedwordpools='y'] .fixedWordBox {
	@include position-coordinates(0, 0, 0, 0);
	@include position-coordinates(
		auto,
		var(--rcfPlayer_fixedPositionOffset_right),
		var(--rcfPlayer_fixedPositionOffset_bottom),
		var(--rcfPlayer_fixedPositionOffset_left)
	);
	opacity: 1;
	opacity: var(--rcfPlayer_wordBox_opacity);
}

/* RCF Mask */
.content :global(.rcfMask),
.content :global(.rcfInteractiveMask) {
	display: none;
	position: absolute;
	@include position-coordinates(0, 0, 0, 0);
	background-color: transparent;
}
.fullScreen .content :global(.rcfMask),
.fullScreen .content :global(.rcfInteractiveMask) {
	top: -48px;
}
.content :global(.rcfMask.show) {
	display: block;
	background: transparent;
	z-index: auto;
}
.content :global(.rcfMask:after),
.content :global(.rcfInteractiveMask:after) {
	content: '';
	background-color: $black;
	opacity: 0.7 !important;
	position: fixed;
	@include position-coordinates(0, 0, 0, 0);
	.windowed & {
		@include position-coordinates($windowed-position-gap, $windowed-position-gap, $windowed-position-gap, $windowed-position-gap);
	}
}
/* Hide items in an item-based activity not currently on-screen.
   Don't use display-none so items retain dimensions (required for RCF to dynamically calculate sizes of some display elements)  */
:global .itemBasedItem:not(.active) {
	max-height: 0;
	overflow: hidden;
}
/*fix non scrollable content hidden behind wordpools in safari*/
.has_fixedWordBox :global(#activity .activityContainer) {
	height: fit-content;
	padding-bottom: 1px;
}
/* fix disabled story button opacity */
:global(.activity.disabled .story .navigation ul .button) {
	opacity: 0.3;
}
/*disable student's audio recording while waiting for teacher's feedback*/
/*WP is adding this to their styles to avoid overwrites*/
/*:global .disabled .largeAudioRecording:not(.teacherRecording) .largeAudioRecordingPanel .audioRecordingControls .audioRecordButton,
:global .disabled .largeAudioRecording:not(.teacherRecording) .largeAudioRecordingPanel .audioRecordingControls .audioStopRecordButton {
	pointer-events: none;
	background-color: $secondary;
}*/

// plyr non-native video fixes
:global(#top-level-details).has_content :global(#activity-content:has(.plyr--fullscreen-fallback)) {
	overflow: revert;
	z-index: 2;
}
:global(#top-level-details).has_content > :global([data-template]:has(.plyr--fullscreen-fallback)) {
	z-index: 1;
}
