// _typography.scss

html {
	font-size: var(--base_fontsize);
}
*[class^='player__'],
button[class^='player__'] {
	font-size: 1rem;
	font-family: var(--base_fontfamily);
}
.noAttemptAvailable h2 {
	font-size: 1.125rem;
	font-weight: 400;
	color: $noAttemptAvailable;
}
.coverScreen .coverScreen_activitySetTitle,
.scoreScreen .scoreScreen_activitySetTitle,
.scoreScreen .scoreScreen_completionText p {
	font-weight: 400;
	font-size: 1.375rem;
	line-height: 1.875rem;
}
.scoreScreen .scoreScreen_individualScoreDetails span,
.scoreScreen .scoreScreen_teacherMarkPendingNotice p {
	font-size: 1.125rem;
}
.scoreScreen_completionText p {
	line-height: 1;
}
.scoreScreen_score .scoreScreen_correctContainer > * {
	font-size: 1rem;
}
