// _variables.scss – common values

// pixel values
$border-radius: 4px;
$windowed-position-gap: 8px;
$hosted-by-scorm-gap: 1px;
$button-offset: calc((48px / 2) - (24px / 2));

// border pre-sets
$grey-border: 1px solid #9a9a9a;

// primary colours
$primary: var(--primary);
$primary_active: var(--primary_active);
$primary_disabled: var(--primary_disabled);
$primary_content: var(--primary_content);
$primary_content_active: var(--primary_content_active);
$primary_content_disabled: var(--primary_content_disabled);
$primary_border: var(--primary_border);
$primary_border_active: var(--primary_border_active);
$primary_border_disabled: var(--primary_border_disabled);

// primary outline colours
$primaryOutline: var(--primaryOutline);
$primaryOutline_active: var(--primaryOutline_active);
$primaryOutline_disabled: var(--primaryOutline_disabled);
$primaryOutline_content: var(--primaryOutline_content);
$primaryOutline_content_active: var(--primaryOutline_content_active);
$primaryOutline_content_disabled: var(--primaryOutline_content_disabled);
$primaryOutline_border: var(--primaryOutline_border);
$primaryOutline_border_active: var(--primaryOutline_border_active);
$primaryOutline_border_disabled: var(--primaryOutline_border_disabled);

// secondary colours
$secondary: var(--secondary);
$secondary_active: var(--secondary_active);
$secondary_disabled: var(--secondary_disabled);
$secondary_content: var(--secondary_content);
$secondary_content_active: var(--secondary_content_active);
$secondary_content_disabled: var(--secondary_content_disabled);
$secondary_border: var(--secondary_border);
$secondary_border_active: var(--secondary_border_active);
$secondary_border_disabled: var(--secondary_border_disabled);

// secondary outline colours
$secondaryOutline: var(--secondaryOutline);
$secondaryOutline_active: var(--secondaryOutline_active);
$secondaryOutline_disabled: var(--secondaryOutline_disabled);
$secondaryOutline_content: var(--secondaryOutline_content);
$secondaryOutline_content_active: var(--secondaryOutline_content_active);
$secondaryOutline_content_disabled: var(--secondaryOutline_content_disabled);
$secondaryOutline_border: var(--secondaryOutline_border);
$secondaryOutline_border_active: var(--secondaryOutline_border_active);
$secondaryOutline_border_disabled: var(--secondaryOutline_border_disabled);

// non-standard button colours
$buttonFocus: var(--buttonFocus);

// non-standard colors
$white: #fff;
$black: #000;
$transparent: transparent;

$noAttemptAvailable: var(--noAttemptAvailable);

$progressBarActive: var(--progressBarActive);
$itemBasedProgressBarProgress: var(--itemBasedProgressBarProgress);
$progressBar_correct: var(--progressBar_correct);
$progressBar_wrong: var(--progressBar_wrong);
$progressBar_pendingMarks: var(--progressBar_pendingMarks);
$progressBar_partiallyCorrect: var(--progressBar_partiallyCorrect);

$coverScoreScreen_backgroundGradient_1: var(--coverScoreScreen_backgroundGradient_1);
$coverScoreScreen_backgroundGradient_2: var(--coverScoreScreen_backgroundGradient_2);
$coverScoreScreen_challenge_backgroundGradient_1: var(--coverScoreScreen_challenge_backgroundGradient_1);
$coverScoreScreen_challenge_backgroundGradient_2: var(--coverScoreScreen_challenge_backgroundGradient_2);

$itemBasedProgressBar_timerContainer_green: var(--itemBasedProgressBar_timerContainer_green);
$itemBasedProgressBar_timerContainer_orange: var(--itemBasedProgressBar_timerContainer_orange);
$itemBasedProgressBar_timerContainer_red: var(--itemBasedProgressBar_timerContainer_red);

$timedPlaylistTimer_red: var(--timedPlaylistTimer_red);

// Common height values for use in calculations
:root {
	--controls_height: 0;
}
.has_controls {
	--controls_height: 60px;
}
