// _mixins.scss – Shortcuts

@mixin position-coordinates($positionTop, $positionRight, $positionBottom, $positionLeft) {
	top: $positionTop;
	right: $positionRight;
	bottom: $positionBottom;
	left: $positionLeft;
}
@mixin border-radius($topLeft, $topRight, $bottomRight, $bottomLeft) {
	border-top-left-radius: $topLeft;
	border-top-right-radius: $topRight;
	border-bottom-right-radius: $bottomRight;
	border-bottom-left-radius: $bottomLeft;
}

// Style mixins – for use in place of extends as extends can get messy fast
@mixin center-flex() {
	display: flex;
	align-items: center;
	justify-content: center;
}
@mixin container-bar() {
	background: $white;
	height: 60px;
	text-align: center;
	flex-shrink: 0;
	// Display over locating pop-up
	z-index: 1;
	@include center-flex();
	> * {
		@include center-flex();
	}
}
@mixin hidden-content() {
	visibility: hidden;
	opacity: 0;
	transition: visibility 0s linear 0.2s, opacity 0.2s linear;
}
