// _icons.scss – generic icon styles

.standardIcon {
	@include center-flex;
	cursor: pointer;
	border: none;
	border-radius: 5px;
	margin: 0;
	padding: 0;
	height: 24px;
	width: 24px;
	position: relative;
	background-color: var(--icon);
	color: var(--icon_content);
	fill: var(--icon_content);

	&:active:not(:disabled) {
		background-color: var(--icon_active);
		color: var(--icon_content_active);
		fill: var(--icon_content_active);
	}
	&:hover:not(:disabled) {
		background-color: var(--icon_hover);
		color: var(--icon_content_hover);
		fill: var(--icon_content_hover);
	}
	&:focus:not(:disabled) {
		background-color: var(--icon_focused);
		color: var(--icon_content_focused);
		fill: var(--icon_content_focused);
		outline: 2px solid var(--icon_focused_outline);
		outline-offset: 2px;
	}
	&:disabled {
		cursor: default;
		background-color: var(--icon_disabled);
		color: var(--icon_content_disabled);
		fill: var(--icon_content_disabled);
	}
}
