// _rcfContentWindow.scss – styling for content and related classes

/* Common Container (Content) Styles */ // I'm not really sure where 'content' should sit
.content,
:global(.slidingContainer .slidingBlock) {
	width: auto;
	max-width: 100%;
}
.content :global(.slidingContainer .slidingBlock) {
	overflow: hidden;
	@include position-coordinates(0, 0, 0, 0);
	@include position-coordinates(
		var(--rcfPlayer_fixedPositionOffset_top),
		var(--rcfPlayer_fixedPositionOffset_right),
		var(--rcfPlayer_fixedPositionOffset_bottom),
		var(--rcfPlayer_fixedPositionOffset_left)
	);
}
.windowed {
	[data-template]:first-child :global(.slidingContainer .slidingHeader) {
		border-top-right-radius: 4px;
	}
	[data-template]:last-child :global(.slidingContainer .slidingBlock) {
		border-bottom-right-radius: 4px;
	}
}

/* Content Container Styles */
.content {
	margin: 0 auto;
	overflow-y: auto;
	flex-grow: 1;
	width: 100%;
	height: calc(100% - var(--controls_height));
	// Fix for RCF-10320 (drag & drop not scrolling) – do not remove
	position: relative;
	z-index: 1;
	.has_rcfActivityActivitySetTitle & {
		.rcfActivity_activitySetTitle ~ :global(#activity) {
			min-height: calc(100% - 48px);
			height: calc(100% - 48px);
		}
	}
}
.contentInner {
	position: relative;
}
.hidden,
.hidden *:before,
.hidden *:after,
.hidden * {
	visibility: hidden !important;
	transition: none !important;
}

.noAttemptAvailable .hidden {
	display: none;
}

/* GradeBook No Attempt Available Text */
.noAttemptAvailable {
	background-color: $white;
	align-items: center;
	justify-content: center;
	.noAttemptAvailable_text {
		flex-grow: 1;
		@include center-flex;
	}
	h2 {
		margin: 0;
		text-align: center;
	}
	.rcfActivity_activitySetTitle {
		width: 100%;
	}
}
