// _controls.scss – All styles for all marking controls

.controls {
	@include container-bar;
	position: relative;
	border-top: $grey-border;
	justify-content: center;
	/*box-shadow: 0 -1px 8px 0 rgba(0, 0, 0, 0.15);*/
	padding: 0 8px;
	width: 100%;

	.titleBar ~ & {
		display: none;
	}

	/* Marking control group styles */
	> * {
		max-width: 136px;
		width: 100%;
	}
	// Control groups minimum spacing
	> * + * {
		margin-left: 4px;
		@media (min-width: 350px) {
			margin-left: 8px;
		}
		@media (min-width: 550px) {
			margin-left: 16px;
		}
	}

	/* Reset control group */
	// Position the 'reset button' to the left hand side when in teacher mode
	&.has_controlsReset {
		> .controls_reset:not(.has_tryAgainButton) + *:nth-child(2) {
			margin-left: 0;
		}
		.controls_reset:not(.has_tryAgainButton) {
			margin-left: 6px;
			margin-right: 8px;
			max-width: 24px;
			&:only-child,
			&:nth-last-child(2) {
				position: absolute;
				left: 8px;
			}
			&:nth-last-child(3) {
				margin-left: 0;
				@media (min-width: 380px) {
					margin-left: 6px;
					position: absolute;
					left: 8px;
				}
			}
			&:nth-last-child(4) {
				margin-left: 0;
				@media (min-width: 380px) {
					margin-left: 6px;
					position: absolute;
					left: 8px;
				}
			}
		}
		&.has_controlsShowAnswers {
			@media (min-width: 380px) {
				padding: 0 8px 0 44px;
			}
			@media (min-width: 610px) {
				padding: 0 44px;
			}
		}
	}
	/* Navigation control groups */
	.has_itemBasedNextButton,
	.has_itemBasedPreviousButton,
	.has_itemBasedFinishButton,
	.has_nextActivitySetButton,
	.has_multiStepNextActivityButton,
	.has_multiStepPreviousActivityButton,
	.has_multiStepNextActivitySetButton,
	.has_multiStepPreviousActivitySetButton,
	.has_multiStepSubmitButton,
	.has_nextTimedPlaylistActivitySetButton,
	.has_previousTimedPlaylistActivitySetButton,
	.has_timedPlaylistFinishButton,
	.has_timedPlaylistSubmitButton, // not used ? (was has_assessmentSubmitButton)
	.has_endPlaylistButton {
		max-width: 38px;
		@media (min-width: 450px) {
			max-width: 136px;
		}
		// Override for when it is the only button on screen
		&:only-child,
		// Override for when there are only 2 buttons on screen
		&:nth-child(2):last-child,
		&:nth-last-child(2):first-child {
			max-width: 136px;
		}
	}
	// Override for when show answers control group is on screen
	&.has_controlsShowAnswers .controls_navigation:nth-child(3) {
		max-width: 38px;
		span {
			display: none;
		}
		@media (min-width: 500px) {
			max-width: 136px;
			span {
				display: block;
			}
		}
	}
	&.has_controlsShowAnswers .controls_navigation:nth-child(4) {
		max-width: 38px;
		span {
			display: none;
		}
		@media (min-width: 700px) {
			max-width: 136px;
			span {
				display: block;
			}
		}
	}
	&.has_checkAnswersButton.has_controlsShowAnswers .controls_navigation:nth-child(4) {
		max-width: 38px;
		span {
			display: none;
		}
		@media (min-width: 650px) {
			max-width: 136px;
			span {
				display: block;
			}
		}
	}
	.has_previousTimedPlaylistActivitySetButton {
		// This is a special case because the text in the previous button is too long at this width
		max-width: 38px;
		span {
			display: none;
		}
		@media (min-width: 580px) {
			max-width: 136px;
			span {
				display: block;
			}
		}
		// Override for when it is the only button on screen
		&:only-child,
		// Override for when there are only 2 buttons on screen
		&:nth-child(2):last-child,
		&:nth-last-child(2):first-child {
			max-width: 136px;
			span {
				display: block;
			}
		}
		// Override for when the itembased start button is also on screen (cover screen)
		.has_itemBasedCoverScreen [data-template='item-based'] &:nth-last-child(2):first-child {
			@media (max-width: 450px) {
				max-width: 38px;
				.previousTimedPlaylistActivitySetButton {
					span {
						display: none;
					}
					svg {
						display: block;
					}
				}
			}
		}
	}
	&.has_controlsShowAnswers .has_previousTimedPlaylistActivitySetButton {
		max-width: 38px;
		span {
			display: none;
		}
		@media (min-width: 700px) {
			max-width: 136px;
			span {
				display: block;
			}
		}
	}
	// Override for when there are only 2 buttons on screen + the reset button
	.has_resetActivityButton + .has_previousTimedPlaylistActivitySetButton + .has_nextTimedPlaylistActivitySetButton,
	.has_resetActivityButton + .has_previousTimedPlaylistActivitySetButton + .has_timedPlaylistFinishButton {
		&:nth-child(3):nth-last-child(1) {
			max-width: 136px;
			span {
				display: block;
			}
		}
	}
	.has_resetActivityButton + .has_previousTimedPlaylistActivitySetButton {
		&:nth-child(2):nth-last-child(2) {
			@media (min-width: 325px) {
				max-width: 136px;
				span {
					display: block;
				}
			}
		}
	}
	// multiStep coverScreen navigation buttons – Always large
	.controls_navigation.has_multiStepStartButton.has_nextActivitySetButton,
	.controls_navigation.has_multiStepStartButton.has_endPlaylistButton {
		max-width: #{(136 * 2) + 8}px;
		@media (min-width: 550px) {
			max-width: #{(136 * 2) + 16}px;
		}
	}

	// item-based cover / score screen with timed-playlist controls - 2 buttons wide as it'll always have start button as well
	.controls_navigation.has_itemBasedStartButton.has_nextTimedPlaylistActivitySetButton,
	.controls_navigation.has_itemBasedStartButton.has_timedPlaylistFinishButton {
		max-width: #{(136 * 2) + 8}px;
		@media (min-width: 550px) {
			max-width: #{(136 * 2) + 16}px;
		}
		.itemBasedStartButton svg {
			display: none;
		}
	}
	// item-based cover / score screen with timed-playlist controls - when previousTimedPlaylistActivitySetButton is also on screen
	.has_previousTimedPlaylistActivitySetButton + .controls_navigation.has_itemBasedStartButton.has_nextTimedPlaylistActivitySetButton,
	.has_previousTimedPlaylistActivitySetButton + .controls_navigation.has_itemBasedStartButton.has_timedPlaylistFinishButton {
		max-width: #{(136 + 38) + 8}px;
		.nextTimedPlaylistActivitySetButton,
		.timedPlaylistFinishButton {
			max-width: 38px;
		}
		@media (min-width: 450px) {
			max-width: #{(136 * 2) + 8}px;
			button span {
				display: block;
			}
			.nextTimedPlaylistActivitySetButton,
			.timedPlaylistFinishButton {
				max-width: 100%;
			}
		}
	}

	/* Check answers button */
	// Override for check answers button when it is the 2nd of 3 buttons has_multiStepSubmitButton
	.has_checkAnswersButton:nth-child(2):nth-last-child(2),
	.has_checkAnswersButton:nth-child(2):nth-last-child(2) + .has_multiStepNextActivitySetButton,
	.has_checkAnswersButton:nth-child(2):nth-last-child(2) + .has_multiStepSubmitButton,
	.has_submitButton:nth-child(2):nth-last-child(2),
	.has_submitButton:nth-child(2):nth-last-child(2) + .has_multiStepNextActivitySetButton,
	.has_submitButton:nth-child(2):nth-last-child(2) + .has_multiStepSubmitButton {
		max-width: 136px;
	}
	// Overide for checkanswers button with reset button & 2 other control groups on screen
	.controls_reset:not(.has_tryAgainButton):nth-last-child(4) ~ .has_checkAnswersButton {
	}
	/* Show answers control group */
	.controls_showAnswers {
		max-width: 147px;
		@media (min-width: 550px) {
			max-width: #{111 + 6 + 130}px;
		}
		@media (min-width: 600px) {
			max-width: #{111 + 6 + 170}px;
		}
		&:nth-child(2) {
			@media (min-width: 380px) {
				max-width: 245px;
			}
			@media (min-width: 550px) {
				max-width: #{111 + 6 + 170}px;
			}
		}
		&.has_showAnswersButton {
			max-width: 142px;
		}
		&.has_answersToggleButton {
			max-width: 164px;
		}
		.showAnswers_Text {
			margin: 0 6px 0 0;
			white-space: nowrap;
			display: none;
			@media (min-width: 550px) {
				display: block;
			}
		}
		&:nth-child(2) .showAnswers_Text {
			display: none;
			@media (min-width: 380px) {
				display: block;
			}
		}
		.showAnswers_buttons {
			@include center-flex;
			width: 100%;
			> * {
				padding: 0 8px;
				@media (min-width: 450px) {
					padding: 0 15px;
				}
			}
		}
		.showOneByOneButton {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
			width: 95%;
			&:hover {
				z-index: 1;
			}
		}
		.showAllButton {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			margin-left: calc(var(--button_border_width) * -1);
			&:hover {
				z-index: 1;
			}
		}
		.showAnswersRevealButton {
			display: block;
			@media (min-width: 550px) {
				display: none;
			}
		}
		&:nth-child(2) .showAnswersRevealButton {
			display: block;
			@media (min-width: 380px) {
				display: none;
			}
		}
		&.has_showAnswersRevealButton .showOneByOneButton,
		&.has_showAnswersRevealButton .showAllButton {
			display: none;
			@media (min-width: 550px) {
				display: block;
			}
		}
		&.has_showAnswersRevealButton:nth-child(2) .showOneByOneButton,
		&.has_showAnswersRevealButton:nth-child(2) .showAllButton {
			display: none;
			@media (min-width: 380px) {
				display: block;
			}
		}
		&.has_showAnswersRevealButton .showOneByOneButton {
			margin-left: 0;
		}
		.showOneByOneButton span:nth-child(1) {
			display: none;
			@media (min-width: 600px) {
				display: block;
			}
		}
		.showOneByOneButton span:nth-child(2) {
			display: block;
			@media (min-width: 600px) {
				display: none;
			}
		}
		.showOneByOneButton:focus:before {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
			z-index: 1;
		}
		.showAllButton:focus:before {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			z-index: 2;
		}
		.showAllItemButton {
			max-width: 164px;
		}
	}
	// Override for when marking controls is on screen with show answers
	.controls_marking + .controls_showAnswers.has_showAnswersText:nth-child(2) {
		max-width: 127px;
		@media (min-width: 415px) {
			max-width: 147px;
		}
		@media (min-width: 550px) {
			max-width: #{111 + 6 + 170}px;
		}
		&.has_showAnswersRevealButton {
			max-width: 136px;
			@media (min-width: 550px) {
				max-width: #{111 + 6 + 170}px;
			}
		}
		.showAnswers_Text {
			display: none;
			@media (min-width: 550px) {
				display: block;
			}
		}
		.showAnswersRevealButton {
			display: block;
			@media (min-width: 550px) {
				display: none;
			}
		}
		&.has_showAnswersRevealButton .showOneByOneButton,
		&.has_showAnswersRevealButton .showAllButton {
			display: none;
			@media (min-width: 550px) {
				display: block;
			}
		}
	}

	// Work around for itemBased review when itemBasedPreviousButton is on screen
	.has_itemBasedPreviousButton ~ .has_showAnswersButton {
		.itemBased_review & {
			@media (max-width: 349px) {
				max-width: calc(
					100% - (38px * 2) - 80px - (4px * 3)
				); // 100% - (previous button & next button) - check answers button - (margins)
				.showAnswers_buttons {
					width: 100%;
					> * {
						overflow: hidden;
						text-overflow: ellipsis;
					}
				}
			}
		}
	}

	// showAnswers_Text & previous button override for when there are 5 buttons on screen
	&.has_controlsMarking.has_controlsShowAnswers.has_controlsNavigation .is_leftSide {
		max-width: 38px;
		min-width: 38px;
		button {
			padding: 0;
			span {
				display: none;
			}
		}
		@media (min-width: 550px) {
			max-width: 136px;
			min-width: 126px;
			button {
				padding: inherit;
				span {
					display: block;
				}
			}
		}
	}
	&.has_controlsMarking.has_controlsShowAnswers.has_controlsNavigation
		.is_leftSide
		~ .controls_showAnswers.has_showAnswersText.has_showAllButton.has_showOneByOneButton {
		@media (min-width: 550px) {
			max-width: 206px;
		}
		@media (min-width: 775px) {
			max-width: #{89 + 6 + 206}px; // #{.showAnswersText width + .showAnswersText margin + .showAnswers_buttons width}px
		}
		&.has_showAnswersRevealButton {
			max-width: 136px;
			@media (min-width: 775px) {
				max-width: #{89 + 6 + 206}px;
			}
		}
		.showAnswers_Text {
			display: none;
			@media (min-width: 775px) {
				display: block;
			}
		}
		.showAnswersRevealButton {
			display: block;
			@media (min-width: 775px) {
				display: none;
			}
		}
		&.has_showAnswersRevealButton .showOneByOneButton,
		&.has_showAnswersRevealButton .showAllButton {
			display: none;
			@media (min-width: 775px) {
				display: block;
			}
		}
	}
}
