	font-family: 'OpenSans';
	src: url('./fonts/Open_Sans/OpenSans-Italic.woff2') format('woff2');
	font-display: auto;
	font-weight: 400;
	font-style: italic;
}
@font-face {
	font-family: 'OpenSans';
	src: url('./fonts/Open_Sans/OpenSans-SemiBold.woff2') format('woff2');
	font-display: auto;
	font-weight: 700;
	font-style: normal;
}
