// _loadingAnimation.scss

.loader {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	align-content: center;
	align-items: center;
	gap: calc(4px * 6);
	width: auto;
	background-color: white;
	border: 2px solid #fff;
	margin: 0;
	z-index: 4;
	position: fixed;
	@include position-coordinates(
		var(--rcfPlayer_fixedPositionOffset_top),
		var(--rcfPlayer_fixedPositionOffset_right),
		var(--rcfPlayer_fixedPositionOffset_bottom),
		var(--rcfPlayer_fixedPositionOffset_left)
	);
	.windowed & {
		@include border-radius(4px, 4px, 4px, 4px);
		margin: 0;
	}
	.loader_block {
		height: 100%;
		max-height: 64px;
		aspect-ratio: 1/1;
		display: block;
		border-radius: 50%;
		background: conic-gradient(#fff 0%, var(--primary));
		-webkit-mask: radial-gradient(farthest-side, transparent calc(100% - 8px), #000 0);
		animation: s3 1s infinite linear;
	}
	.loader_title {
		display: block;
		color: #000;
	}
	@keyframes s3 {
		to {
			transform: rotate(1turn);
		}
	}
}
