// _theme.scss – The theme for the player when the .theme_calmBlue class is present

.theme_calmBlue,
:global(.theme_calmBlue) {
	// fonts
	--base_fontsize: 16px;
	--base_fontfamily: 'Castledown', 'DejaVuSans', Arial, Helvetica, sans-serif;

	// borders
	--button_border_width: 2px;
	--button_border-radius: 8px;

	// colours from UX
	--ux-color-primary-hover: #060b15; // Colors/Primary/color-primary-hover
	--ux-color-primary: #3c6ed2; // Colors/Primary/color-primary
	--ux-color-primary-disabled: #b1c5ed; // Colors/Primary/color-primary-disabled
	--ux-color-primary-pressed: #24427e; // Colors/Primary/color-primary-pressed
	--ux-on-primary: #ffffff; // Colors/Primary/on-primary
	--ux-b950: #060b15; // Blue/B950
	--ux-n0: #ffffff; // Neutral/N0
	--ux-n100: #e3e3e3; // Neutral/N100
	--ux-n1000: #000000; // Neutral/N1000
	--ux-n400: #8e8e8e; // Neutral/N400
	--ux-n50: #f1f1f1; // Neutral/N50
	--ux-b50: #ecf1fb; // Blue/B50
	--ux-b200: #b1c5ed; // Blue/B200
	--ux-background-colour: var(--ux-b50);
	--ux-icon-colour-dark: var(--ux-color-primary);
	--ux-icon-colour-light: var(--ux-b200);
	--ux-item-active: #b1c5ed; // Colors/items/item-active

	// primary colours
	--primary: var(--ux-color-primary); // default colour
	--primary_border: var(--ux-color-primary);
	--primary_content: var(--ux-n0); // text colour

	--primary_active: var(--ux-color-primary-pressed); // active colour
	--primary_border_active: var(--ux-color-primary-pressed);
	--primary_content_active: var(--ux-n0); // active/hover/focused text colour

	--primary_hover: var(--ux-color-primary-hover); // hover colour
	--primary_border_hover: var(--ux-color-primary-hover);

	--primary_disabled: var(--ux-color-primary-disabled); // disabled colour
	--primary_border_disabled: var(--ux-color-primary-disabled);
	--primary_content_disabled: var(--ux-n0); // disabled text colour

	--primary_focused: var(--ux-color-primary); // focused colour
	--primary_border_focused: var(--ux-color-primary);
	--primary_focused_outline: var(--ux-b950);

	// primary outline colours – these aren't really used, only the border colours seems to be used
	// none of these colours seem to refer to the Foundation Colours document so we should leave them?
	--primaryOutline: #fff;
	--primaryOutline_border: #1b60f4;
	--primaryOutline_content: #1b60f4;

	--primaryOutline_active: #8daff9;
	--primaryOutline_border_active: #0b4ddc;
	--primaryOutline_content_active: #0b4ddc;

	--primaryOutline_disabled: #fff;
	--primaryOutline_border_disabled: #8daff9;
	--primaryOutline_content_disabled: #8daff9;

	// secondary colours
	--secondary: var(--ux-n0);
	--secondary_border: var(--ux-color-primary);
	--secondary_content: var(--ux-n1000);

	--secondary_active: var(--ux-n0);
	--secondary_border_active: var(--ux-color-primary-pressed);
	--secondary_content_active: var(--ux-n1000);

	--secondary_hover: var(--ux-n0);
	--secondary_border_hover: var(--ux-b950);

	--secondary_disabled: var(--ux-n0);
	--secondary_border_disabled: var(--ux-color-primary-disabled);
	--secondary_content_disabled: var(--ux-n400);

	--secondary_focused: var(--ux-n0);
	--secondary_border_focused: var(--ux-color-primary);
	--secondary_focused_outline: var(--ux-b950);

	// secondary outline colours
	--secondaryOutline: var(--ux-n0);
	--secondaryOutline_border: #8b8b8b;
	--secondaryOutline_content: var(--ux-n1000);

	--secondaryOutline_active: #f6f6f6;
	--secondaryOutline_border_active: #8b8b8b;
	--secondaryOutline_content_active: #212529;

	--secondaryOutline_disabled: var(--ux-n0);
	--secondaryOutline_border_disabled: #c7c7c7;
	--secondaryOutline_content_disabled: #8b8b8b;

	// icon colours
	// standard icon colours
	--icon: #fff;
	--icon_active: #e2e2e2;
	--icon_hover: #e2e2e2;
	--icon_disabled: #fff;
	--icon_focused: #e2e2e2;
	--icon_content: #000;
	--icon_content_active: #000;
	--icon_content_hover: #000;
	--icon_content_disabled: #e2e2e2;
	--icon_content_focused: #000;
	--icon_focused_outline: var(--ux-b950);

	// non-standard button colours
	--buttonFocus: #c6d7fc;

	// gradebook colours
	--noAttemptAvailable: #1a051d;

	// progress bar colours
	--progressBarActive: #fcdb4a;
	--itemBasedProgressBarProgress: #d1d1d1;
	--progressBar_correct: #00a417;
	--progressBar_wrong: #eb0104;
	--progressBar_pendingMarks: #fed119;
	--progressBar_partiallyCorrect: #eb0104;
	--itemBasedProgressBar_timerContainer_green: #00a417;
	--itemBasedProgressBar_timerContainer_orange: #f0ab35;
	--itemBasedProgressBar_timerContainer_red: #eb0104;
	--itemBasedProgressBar_lives: #1b60f4;
	--itemBasedProgressBar_correctCounter: #1b60f4;
	--timedPlaylistTimer_red: #eb0104;

	// progress pagination bar colours
	--paginationBar: var(--ux-n50);
	--paginationBar_attempted: var(--ux-color-primary-disabled);
	--paginationBar_active: var(--ux-color-primary);
	--paginationBar_hover: #000;

	// cover/score screen colours
	--coverScoreScreen_rules_text: #1b60f4;
	--coverScoreScreen_rules_border: #8b8b8b;
	--coverScoreScreen_rules_background: #f6f6f6;
	--coverScoreScreen_teachersMode_text: #fff;
	--coverScoreScreen_teachersMode_background: #1b60f4;
	--coverScoreScreen_icons: #1b60f4;
	--coverScoreScreen_linkStep: #1b60f4;
	--coverScoreScreen_backgroundGradient_1: var(--ux-background-colour);
	--coverScoreScreen_backgroundGradient_2: var(--ux-background-colour);
	--coverScoreScreen_challenge_backgroundGradient_1: var(--ux-background-colour);
	--coverScoreScreen_challenge_backgroundGradient_2: var(--ux-background-colour);

	// cover/score screen svg sizes
	--coverScoreScreen_svg_mobile: 188px;
	--coverScoreScreen_svg_desktop: 188px;

	// scoreCard
	--scoreCard_text: #fff;
	--scoreCard_background: #4a4a4a;
	--scoreCard_hover_text: #212529;
	--scoreCard_hover_background: #e2e2e2;

	// activitySet
	--navigationArrowsContainer_separator: #c7c7c7;

	// windowed mode drag handle images
	--dragHandle_background_image: url('images/calmBlue_dragCornerTopLeft.svg'), url('images/calmBlue_dragCornerTopRight.svg'),
		url('images/calmBlue_dragCornerBottomRight.svg'), url('images/calmBlue_dragCornerBottomLeft.svg'),
		url('images/calmBlue_dragGutter.svg'), url('images/calmBlue_dragGutter.svg'), url('images/calmBlue_dragGutter.svg'),
		url('images/calmBlue_dragGutter.svg');
}
