// _activitySet.scss – All styles for activitySetNavigation & activitySetTitle

.activitySetNavigation,
.rcfActivity_activitySetTitle {
	@include container-bar;
	// Display over locating pop-up
	z-index: 1;
}
.activitySetNavigation {
	position: relative;
	height: 48px;
}
[data-link-origin='assessment'] .activitySetNavigation.timedPlaylistInProgress,
[data-link-origin='strictHomework'] .activitySetNavigation.timedPlaylistInProgress {
	height: auto;
	flex-direction: column;
	@media (min-width: 450px) {
		height: 48px;
	}
}
.activitySetNavigation_closeButton,
.navigationArrowsContainer {
	position: absolute;
}
.timedPlaylistTimer {
	width: 72px;
	height: 40px;
	font-weight: 700;
	> span {
		font-size: 1.125rem;
		margin-top: 8px;
	}
	@media (min-width: 450px) {
		position: absolute;
		left: $button-offset;
		height: unset;
		> span {
			margin-top: unset;
		}
	}
}
// only show the timed playlist timer for strictHomework when <= 5 minutes remaining
[data-link-origin='strictHomework'] .timedPlaylistTimer.timedPlaylistTimer_hideInStrictHomework {
	display: none;
}
.activitySetNavigation {
	border-bottom: $grey-border;
}
.activitySetNavigation_closeButton {
	right: $button-offset;
}
.navigationArrowsContainer {
	left: $button-offset;
}
.navigationArrowsContainer .activitySetNavigation_nextButton {
	margin-left: 24px;
}
.navigationArrowsContainer:before {
	content: '';
	position: absolute;
	left: 50%;
	height: 32px;
	width: 1px;
	background: var(--navigationArrowsContainer_separator);
}
.extendedActivitySetNavigation {
	.activitySet_dragIcon {
		position: absolute;
		left: $button-offset;
	}
	.activitySetNavigation_previousButton {
		margin-right: 10px;
	}
	.activitySetNavigation_nextButton {
		margin-left: 10px;
	}
	.activitySet_Buttons {
		position: absolute;
		right: calc((48px / 2) - (24px / 2));
		.activitySetNavigation_closeButton {
			position: static;
			right: auto;
			margin-left: 10px;
		}
	}
}
// Used in timed playlist (assessment and strict homework) activity set navigation
.activitySet_titleBarButtons {
	position: absolute;
	right: calc((48px / 2) - (24px / 2));
	top: calc(48px / 2 - 24px / 2);
	@media (min-width: 450px) {
		top: unset;
	}
	.activitySetNavigation_closeButton {
		position: static;
		right: auto;
		margin-left: 10px;
	}
	&:only-child {
		justify-content: flex-end;
	}
}
.rcfActivity_activitySetTitle {
	position: relative;
	border-bottom: $grey-border;
	height: 48px;
	padding: 3px 8px;
	line-height: initial;
	> span {
		display: inline;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		@supports (-webkit-line-clamp: 2) {
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			white-space: normal;
		}
	}
}
@media only screen and (max-width: 250px) {
	.activitySetNavigation_paginationText {
		display: none;
	}
}

/* Extended ActivitySet Navigation Electron dragging Styles */
/*Allow dragging from extendedActivitySetNavigation when windowed*/
.windowed {
	.extendedActivitySetNavigation {
		-webkit-app-region: drag;
		-webkit-user-select: none;
		.activitySet_dragIcon {
			-webkit-app-region: drag;
			-webkit-user-select: none;
			cursor: grab;
			&:active {
				cursor: grabbing;
			}
		}
		.activitySet_Buttons,
		button {
			-webkit-app-region: no-drag;
		}
	}
}

/* prevent dragging when maximized and add disabled styles to dragging icon */
.maximized {
	.extendedActivitySetNavigation {
		-webkit-app-region: no-drag;
		-webkit-user-select: none;
		.activitySet_dragIcon {
			-webkit-app-region: no-drag;
			-webkit-user-select: none;
			& svg path {
				stroke: var(--icon_content_disabled);
			}
		}
	}
}
