// _progressBar.scss – Styles for all progress bars

// Force center the container when there is room on screen
.itemBasedHeader_backButton + *,
.multiStepHeader_backButton + * {
	min-width: calc(100% - (36px * 2) - (10px * 2)); // 100% - (icon width * 2) - horizontal margin
	.has_titleBarButtons & {
		min-width: calc(100% - (70px * 2) - (10px * 2)); // 100% - (icon width * 2) - horizontal margin
	}
}
.timedPlaylistTimer + * {
	min-width: calc(
		100% - (24px * 2) - (10px * 2) - (75px * 2)
	); // 100% - (icon width * 2) - timer margin - horizontal margin  - timer width
	width: 100%;
	height: 48px;
	@media (min-width: 450px) {
		height: 100%;
		width: calc(
			100% - (24px * 2) - (10px * 2) - (75px * 2)
		); // 100% - (icon width * 2) - timer margin - horizontal margin  - timer width
	}
}

/* Item Based Progress Bar Styles */
.itemBasedProgressBar_challenge {
	display: none;
	flex-wrap: wrap;
	overflow: hidden;
}
.itemBasedProgressBar_reviewProgress,
.itemBasedProgressBar_challenge,
.itemBasedProgressBar_progress,
.multiStepProgressBar_progress,
.timedPlaylistProgressBar_progress,
.itemBasedProgressBar_lives,
.scoreScreen_lives,
.multiStepProgressBar_progress {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	margin: 0 10px;
	.has_titleBarButtons & {
		margin-left: 44px;
	}
}

.timedPlaylistTimer_red {
	color: $timedPlaylistTimer_red;
}

[data-activity-mode='itemBased_challenge'] {
	.itemBasedProgressBar_challenge {
		@media only screen and (max-width: 300px) {
			flex-direction: column;
			flex-wrap: nowrap;
		}
	}
	.itemBasedProgressBar_lives,
	.itemBasedProgressBar_progress {
		margin: 0;
	}
	.itemBasedProgressBar_lives {
		width: 30%;
		max-width: 120px;
	}
	.itemBasedProgressBar_progress {
		width: 70%;
	}
}
.itemBasedProgressBar_reviewProgress li,
.itemBasedProgressBar_progress li,
.multiStepProgressBar_progress li,
.timedPlaylistProgressBar_progress li,
.itemBasedProgressBar_lives li,
.scoreScreen_lives li {
	max-width: 25px;
	flex-grow: 1;
	flex-shrink: 1;
	margin: 4px;
}
.itemBasedProgressBar_lives li {
	height: 25px;
}
.itemBasedProgressBar_lives,
.scoreScreen_lives {
	li :global(polygon.life-active) {
		fill: transparent;
	}
	li :global(path.life-container) {
		fill: $black;
	}
	li :global(circle.life-backgroundContainer) {
		fill: $secondaryOutline_active;
	}
}
.itemBasedProgressBar_reviewProgress li.progressBar_active {
	background-color: $progressBarActive;
}

.itemBasedProgressBar_progress,
.multiStepProgressBar_progress,
.timedPlaylistProgressBar_progress,
.itemBasedProgressBar_reviewProgress {
	li {
		background-color: $itemBasedProgressBarProgress;
		border: solid 2px $transparent;
		border-radius: 8px;
		max-width: 24px;
		aspect-ratio: 2 / 1;
		@supports not (aspect-ratio: 2 / 1) {
			max-width: 16px;
			height: 8px;
			@media only screen and (min-width: 575px) {
				max-width: 24px;
				height: 12px;
			}
		}
	}
	li.progressBar_active {
		border: solid 2px $white;
		outline: 0;
		box-shadow: 0 0 0 2px $primaryOutline_border;
	}
}

.timedPlaylistProgressBar_progress {
	li {
		cursor: pointer;
	}
	li:hover {
		border: solid 2px $white;
		outline: 0;
		box-shadow: 0 0 0 2px $primaryOutline_border;
	}
	li.progressBar_attempted {
		background-color: $primary_active;
	}
}
.timedPlaylistProgressPagination_pagination {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 8px;
	height: 47px;
	margin: 0 10px;
	@media (max-width: 449px) {
		[data-link-origin='strictHomework'] .timedPlaylistTimer_hideInStrictHomework + & {
			// Align to screen left
			max-width: calc(100% - 90px);
			margin: 0 80px 0 10px;
		}
		[data-link-origin='strictHomework'].fullScreen .timedPlaylistTimer_hideInStrictHomework + & {
			max-width: calc(100% - 56px);
			margin: 0 46px 0 10px;
		}
	}
	.has_titleBarButtons & {
		margin-left: 44px;
	}
	button,
	.progressPagination_ellipsis {
		width: unset;
		max-width: unset;
		aspect-ratio: 1 / 1;
		height: 30px;
		margin: 0;
		padding: 0;
		text-align: center;
	}
	.progressPagination_finishButton {
		aspect-ratio: 2 / 1;
	}
	.progressPagination_button {
		cursor: pointer;
		border-radius: 4px;
		background-color: var(--paginationBar);
		border: 1px solid var(--paginationBar);
		color: var(--secondary_content);
		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}
		&:focus:not(:disabled),
		&:hover {
			transition: all 0.3s ease;
		}
		&:focus:not(:disabled) {
			outline: 2px solid var(--secondary_focused_outline);
			outline-offset: 2px;
		}
		@media (hover: hover) {
			&:hover {
				background-color: var(--paginationBar_hover);
				border-color: var(--paginationBar_hover);
				border-width: var(--button_border_width);
				color: var(--primary_content);
			}
		}
	}
	.progressPagination_active {
		background-color: var(--paginationBar_active);
		border-color: var(--paginationBar_active);
		border-width: var(--button_border_width);
		color: var(--primary_content);
		// Disable button when in active state
		cursor: default;
		pointer-events: none;
	}
	.progressPagination_attempted,
	.progressPagination_active.progressPagination_finishButton {
		background-color: var(--paginationBar_attempted);
		border-color: var(--paginationBar_attempted);
		color: var(--secondary_content);
	}
	@media (hover: hover) {
		.progressPagination_attempted:hover {
			background-color: var(--paginationBar_hover);
			border-color: var(--paginationBar_hover);
			color: var(--primary_content);
		}
	}
	.progressPagination_attempted.progressPagination_active {
		background-color: var(--paginationBar_active);
		border-color: var(--paginationBar_active);
		color: var(--primary_content);
	}
	@media (hover: hover) {
		.progressPagination_attempted.progressPagination_active:hover {
			background-color: var(--paginationBar_hover);
			border-color: var(--paginationBar_hover);
		}
	}
	.timedPlaylistProgressPagination_previous,
	.timedPlaylistProgressPagination_next {
		display: static;
	}
}

.itemBasedProgressBar_progress,
.multiStepProgressBar_progress {
	li.progressBar_correct {
		background-color: $progressBar_correct;
	}
	li.progressBar_wrong {
		background-color: $progressBar_wrong;
	}
	li.progressBar_pendingMarks {
		background-color: $progressBar_pendingMarks;
	}
	li.progressBar_partiallyCorrect {
		background-color: $progressBar_pendingMarks;
		background-image: linear-gradient(
			135deg,
			$progressBar_wrong 0%,
			$progressBar_wrong 45%,
			$white 45%,
			$white 55%,
			$progressBar_correct 55%,
			$progressBar_correct 100%
		);
		background-origin: border-box;
	}
}

.itemBasedProgressBar_lives li.progressBar_active {
	:global(polygon.life-active) {
		fill: var(--itemBasedProgressBar_lives);
	}
	:global(path.life-container) {
		fill: transparent;
	}
}

/* challenge timer styles START */

.span.itemBasedProgressBar_timer {
	display: flex;
	justify-content: center;
	align-items: center;
}
[data-activity-mode='itemBased_challenge'] span.itemBasedProgressBar_timer {
	width: auto;
	font-size: 1.313rem;
}
[data-activity-mode='itemBased_challenge'] .itemBasedProgressBar_lives {
	margin-left: 16px;
}
[data-activity-mode='itemBased_challenge'] .itemBasedProgressBar_progress {
	width: 45%;
	min-width: 140px;
	margin-left: 16px;
	max-width: fit-content;
	min-width: 310px;

	@media only screen and (max-width: 300px) {
		display: none;
	}
	@media only screen and (max-width: 460px) {
		width: 100%;
		margin-left: 0;
	}
}

/* timer graph styles */
.itemBasedProgressBar_timerContainer,
.itemBasedProgressBar_timerContainer * {
	transition: 0.3s ease;
}
.itemBasedProgressBar_timerContainer {
	@include center-flex();
}

/* pie styles */
.itemBasedProgressBar_timerContainer svg {
	margin: 0 8px 0 0;
}
.itemBasedProgressBar_red svg circle:last-child {
	stroke: $itemBasedProgressBar_timerContainer_red;
}
.itemBasedProgressBar_orange svg circle:last-child {
	stroke: $itemBasedProgressBar_timerContainer_orange;
}
.itemBasedProgressBar_green svg circle:last-child {
	stroke: $itemBasedProgressBar_timerContainer_green;
}

/*digital timer styles*/
.itemBasedProgressBar_challenge .itemBasedProgressBar_timer {
	font-weight: 700;
}
.itemBasedProgressBar_red .itemBasedProgressBar_timer {
	color: $itemBasedProgressBar_timerContainer_red;
}
.itemBasedProgressBar_orange .itemBasedProgressBar_timer {
	color: $itemBasedProgressBar_timerContainer_orange;
}
.itemBasedProgressBar_green .itemBasedProgressBar_timer {
	color: $itemBasedProgressBar_timerContainer_green;
}

/*animation*/
.itemBasedProgressBar_bounce .itemBasedProgressBar_timer {
	animation: bounceAnimation 0.5s;
}
.itemBasedProgressBar_shortBounce .itemBasedProgressBar_timer {
	animation: shortBounceAnimation 1s;
	animation-iteration-count: infinite;
}

/* live lost animation */
.itemBasedProgressBar_lives li:not(.progressBar_active) {
	animation: bounceAnimation 0.5s;
}

/*animations*/
@keyframes bounceAnimation {
	20%,
	80% {
		transform: scale(0.8);
	}
	50% {
		transform: scale(1.1);
	}
	100% {
		transform: scale(1);
	}
}
@keyframes shortBounceAnimation {
	00% {
		transform: scale(1.1);
	}
	50% {
		transform: scale(1);
	}
}

/*progressive challenge counter*/
.itemBasedProgressBar_counterContainer {
	margin-left: 16px;
	.itemBasedProgressBar_correctCounter {
		font-weight: 700;
		color: var(--itemBasedProgressBar_correctCounter);
		font-size: 1.5rem;
	}
}
@media only screen and (max-width: 380px) {
	[data-challenge-mode='progressive'] .itemBasedProgressBar_lives,
	[data-challenge-mode='progressive'] .itemBasedProgressBar_counterContainer {
		margin-left: 16px;
	}
}
@media only screen and (max-width: 660px) {
	/*itemBased_challenge collapsed progress bars*/
	[data-challenge-mode='random'] .itemBasedProgressBar_timerContainer,
	[data-challenge-mode='random'] .itemBasedProgressBar_lives {
		height: 45%;
		margin-top: 4px;
	}
	[data-challenge-mode='random'] .itemBasedProgressBar_progress {
		height: 45%;
		min-width: 100%;
		margin-left: 0;
	}
}

/* pagination area loader - appears until PLAYER_STATUS.rcfLoaded is set */
.paginationLoader {
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
}

.paginationLoader div {
	position: absolute;
	top: 33px;
	width: 13px;
	height: 13px;
	border-radius: 50%;
	background: #aaa;
	animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.paginationLoader div:nth-child(1) {
	left: 8px;
	animation: paginationLoaderAnimation1 0.6s infinite;
}
.paginationLoader div:nth-child(2) {
	left: 8px;
	animation: paginationLoaderAnimation2 0.6s infinite;
}
.paginationLoader div:nth-child(3) {
	left: 32px;
	animation: paginationLoaderAnimation2 0.6s infinite;
}

.paginationLoader div:nth-child(4) {
	left: 56px;
	animation: paginationLoaderAnimation3 0.6s infinite;
}

@keyframes paginationLoaderAnimation1 {
	0% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}

@keyframes paginationLoaderAnimation2 {
	0% {
		transform: translate(0, 0);
	}
	100% {
		transform: translate(24px, 0);
	}
}

@keyframes paginationLoaderAnimation3 {
	0% {
		transform: scale(1);
	}
	100% {
		transform: scale(0);
	}
}
