// _scoreCard.scss – All styles for scoreCard

.scoreCard {
	@include container-bar;
	position: fixed;
	@include position-coordinates(
		auto,
		var(--rcfPlayer_fixedPositionOffset_right),
		var(--rcfPlayer_fixedPositionOffset_bottom),
		var(--rcfPlayer_fixedPositionOffset_left)
	);
	z-index: 3;
	color: var(--scoreCard_text);
	background: var(--scoreCard_background);
	border-top: $grey-border;
}
.scoreCard_closeButton {
	position: absolute;
	&.standardIcon {
		left: $button-offset;
		color: var(--scoreCard_text);
		fill: var(--scoreCard_text);
		background: transparent;
		svg {
			color: var(--scoreCard_text);
			fill: var(--scoreCard_text);
		}
	}
	&.standardIcon:active:not(:disabled),
	&.standardIcon:hover:not(:disabled),
	&.standardIcon:focus {
		color: var(--scoreCard_hover_text);
		fill: var(--scoreCard_hover_text);
		background-color: var(--scoreCard_hover_background);
		svg {
			color: var(--scoreCard_hover_text);
			fill: var(--scoreCard_hover_text);
		}
	}
}
.scoreCard_scoreText {
	margin: 0 calc(24px + $button-offset * 2);
}
