// _header.scss – Styles for all header elements

/* itemBased header styles */
.itemBasedHeader,
.multiStepHeader {
	@include container-bar;
	border-bottom: $grey-border;
	justify-content: flex-start;
	height: 48px;
	z-index: 1;

	&.has_titleBarButtons {
		.header_titleBarButtons {
			margin-right: 12px;
		}
		.titleBar_fullscreenToggleButton {
			margin: 0;
		}
		.header_closeButton {
			margin-left: 10px;
		}
	}
}

.itemBasedHeader_backButton,
.itemBasedHeader button:first-child,
.multiStepHeader_backButton,
.multiStepHeader button:first-child {
	margin-left: 12px;
}

@media only screen and (max-width: 660px) {
	[data-challenge-mode='random'] .itemBasedHeader {
		height: 64px;
	}
}
